import g from '~/globals';
import C from 'Constants';
import platform from 'platform';
import React from 'react';
import MooReact from 'MooReact';
import FancyModal from 'Components/FancyModal';
import FancyButton from 'Components/FancyButton';
import FancyLoader from 'Components/FancyLoader';

var windowPad = 10,
    bottomLineHeight = 30,
    minSize = 500,
    maxSize = 1000;

var ReactModalLightbox = MooReact.createClass({
    displayName: 'ReactModalLightbox',
    componentDisplay: C.ComponentDisplay.Desktop,
    init: function init() {
        this.createObservables({
            isLoaded: false,
            error: false
        });
    },
    onLoaded: function onLoaded() {
        this.isLoaded.set(true);
    },
    onError: function onError() {
        this.isLoaded.set(true, /*notify*/false);
        this.error.set('Could not load image');
    },
    openExternal: function openExternal() {
        if (this.props.buttonAction) {
            this.props.buttonAction();
        } else {
            g.openUrl(this.props.src);
        }
        this.close();
    },
    close: function close() {
        this.refs.modal.close();
    },
    render: function render() {
        var props = this.props,
            state = this.state,
            src = props.src,
            maxWidth = Math.min(platform.windowWidth() * 0.95, 900),
            maxHeight = Math.min(platform.windowHeight() * 0.95 - 30, 900),
            styleImg = {
            maxWidth: maxWidth,
            maxHeight: maxHeight,
            visibility: !state.isLoaded && 'hidden'
        },
            cls = g.classNames({ 'loaded': state.isLoaded });

        return React.createElement(
            FancyModal,
            { id: 'modalLightbox', className: cls, closeButton: false, scrollable: false, modalID: Modals.Lightbox, ref: 'modal' },
            React.createElement(
                'div',
                { key: 'content' },
                !state.isLoaded && React.createElement(FancyLoader, { center: true }),
                !state.error && React.createElement('img', { src: src, style: styleImg, onLoad: this.onLoaded, onError: this.onError, onClick: this.close, ref: 'image' }),
                state.error && React.createElement(
                    'div',
                    { className: 'lightboxText' },
                    state.error
                ),
                state.isLoaded && React.createElement(
                    FancyButton,
                    { id: 'lightboxOpen', className: 'c-border-heavy', primary: true, height: bottomLineHeight, onClick: this.openExternal },
                    props.buttonText || (platform.app ? 'Open in browser' : 'Open in new tab')
                )
            )
        );
    }
});

export default ReactModalLightbox;