import g from '~/globals';
import C from 'Constants';
import React from 'react';
import MooReact from 'MooReact';
import platform from 'platform';
import International from 'International';
import FancyCheckbox from 'Components/FancyCheckbox';
import FancyInputNumber from 'Components/FancyInputNumber';

var ReactModalSettingsDates = MooReact.createClass({
    displayName: 'ReactModalSettingsDates',
    componentDisplay: C.ComponentDisplay.Desktop,
    init: function init() {},
    onChangeDateFormat: function onChangeDateFormat() {
        var newDateFormat = this.refs.selectDateFormat.value;

        g.settings.set(Settings.dateFormat, newDateFormat);
    },
    onChangeDayWeekStart: function onChangeDayWeekStart() {
        g.settings.set(Settings.dayWeekStart, +this.refs.selectDayWeekStart.value);
    },
    onNotificationsChanged: function onNotificationsChanged(isEnabled) {
        var _this = this;

        g.settings.set(Settings.disableNotifications, !isEnabled);

        if (isEnabled) {
            if (platform.usesHTML5Notifications()) {
                if (!platform.hasNotificationPermission()) {
                    Notification.requestPermission(function (permission) {
                        if (permission === 'granted') {
                            g.timeline.updateNotifications();
                        } else {
                            g.settings.set(Settings.disableNotifications, true);

                            _this.refs.notifications.setValue(false);

                            g.toasts.pushMessage({
                                text: 'To enable notifications in the future, you must first give permission in your browser.',
                                action: MessageAction.OpenURL,
                                actionText: 'LEARN MORE',
                                data: { url: 'https://support.google.com/chrome/answer/6148059' }
                            });
                        }
                    });
                }
            } else {
                g.timeline.updateNotifications();
            }
        } else {
            g.timeline.clearNotifications();
        }
    },
    onDelayChanged: function onDelayChanged(value) {
        g.settings.set(Settings.notificationDelay, value);

        g.sendEvent('NotificationDelay', 'Changed', value);
    },
    onCustomDaysChanged: function onCustomDaysChanged(value) {
        g.settings.set(Settings.calendarCustomDays, value);
    },
    render: function render() {
        var dateFormat = International.getCurrentDateFormat(),
            dayWeekStart = g.settings.get(Settings.dayWeekStart),
            calendarCustomDays = g.settings.get(Settings.calendarCustomDays),
            notificationDelay = g.settings.get(Settings.notificationDelay),
            notificationsEnabled = !g.settings.get(Settings.disableNotifications) && platform.hasNotificationPermission();

        return React.createElement(
            'div',
            { key: 'content' },
            React.createElement(
                'div',
                { className: 'modal-row' },
                React.createElement(
                    'div',
                    { className: 'settingsName ib bold' },
                    'Date Format:'
                ),
                React.createElement(
                    'select',
                    { defaultValue: dateFormat, ref: 'selectDateFormat', onChange: this.onChangeDateFormat },
                    React.createElement(
                        'option',
                        { value: C.DateFormat.MDY },
                        'Month / Day / Year'
                    ),
                    React.createElement(
                        'option',
                        { value: C.DateFormat.DMY },
                        'Day / Month / Year'
                    ),
                    React.createElement(
                        'option',
                        { value: C.DateFormat.YMD },
                        'Year / Month / Day'
                    )
                )
            ),
            React.createElement(
                'div',
                { className: 'modal-row' },
                React.createElement(
                    'div',
                    { className: 'settingsName ib bold' },
                    'Start week on:'
                ),
                React.createElement(
                    'select',
                    { defaultValue: dayWeekStart, ref: 'selectDayWeekStart', onChange: this.onChangeDayWeekStart },
                    React.createElement(
                        'option',
                        { value: 0 },
                        'Sunday'
                    ),
                    React.createElement(
                        'option',
                        { value: 1 },
                        'Monday'
                    )
                )
            ),
            React.createElement(
                'div',
                { className: 'modal-row' },
                React.createElement(
                    'div',
                    { className: 'settingsName ib bold' },
                    'Number of days in custom calendar view (Premium):'
                ),
                React.createElement(FancyInputNumber, { value: calendarCustomDays, minValue: 1, maxValue: 14, readonly: !g.billingManager.hasPremiumFeatures(), onChange: this.onCustomDaysChanged })
            ),
            platform.supportsNotifications() && React.createElement(
                React.Fragment,
                null,
                React.createElement('hr', null),
                React.createElement(
                    'div',
                    { className: 'modal-row' },
                    React.createElement(
                        FancyCheckbox,
                        { ref: 'notifications', checked: notificationsEnabled, onChange: this.onNotificationsChanged },
                        'Enable notifications'
                    ),
                    React.createElement(
                        'div',
                        { className: 'modal-subtext c-foreground-faded' },
                        'Display native notifications for items that use a specific @date and time in their text. These are only displayed for the currently open document on this device.'
                    )
                ),
                React.createElement(
                    'div',
                    { className: 'modal-row' },
                    React.createElement(
                        'span',
                        { className: 'settingsName ib bold' },
                        'Notification Time'
                    ),
                    React.createElement(FancyInputNumber, { value: notificationDelay, minValue: 0, maxValue: 120, interval: 5, onChange: this.onDelayChanged }),
                    React.createElement(
                        'div',
                        { className: 'modal-subtext c-foreground-faded' },
                        'Time in minutes before event to display a notification about the event.'
                    )
                )
            )
        );
    }
});

export default ReactModalSettingsDates;