    getState: function getState() {
        var BM = g.billingManager;

        var state = {
            validBilling: BM._hasValidBillingData,
            premiumStatus: BM._premiumStatus,
            daysLeftInTrial: BM.daysLeftInTrial,
            trialOverHidden: g.settings.get(Settings.trialOverHidden),
            monthlyInvoice: g.settings.get(Settings.monthlyInvoice),
            invoiceEmail: g.settings.get(Settings.invoiceEmail),
            invoiceCompany: g.settings.get(Settings.invoiceCompany),
            invoiceAddress: g.settings.get(Settings.invoiceAddress),
            isUnsubscribed: g.settings.get(Settings.isUnsubscribed)
        };

        return state;
    },
    onClickDismissTrial: function onClickDismissTrial() {
        g.settings.set(Settings.trialOverHidden, true);

        this.updateState();
    },
    onUnsubscribedChanged: function onUnsubscribedChanged(value) {
        var oldValueUnsub = g.settings.get(Settings.isUnsubscribed);

        g.settings.set(Settings.isUnsubscribed, value);

        if (value) {
            g.XHR_PrivateAPI({
                type: 'POST',
                path: '/feedback/unsubscribe',
                data: { email: g.vmMain.getUserEmail() },
                requireAuth: true,
                cb: function (xhr) {
                    if (xhr.status !== C.HTTPStatusCode.Success) {
                        g.settings.set(Settings.isUnsubscribed, oldValueUnsub);

                        g.toasts.pushMessage({
                            text: 'There was an error updating your subscription status. Please retry or contact support@moo.do.',
                            actionText: 'RETRY',
                            timeout: 8000,
                            action: function () {
                                this.onUnsubscribedChanged(value);
                            }.bind(this)
                        });
                    } else {
                        g.toasts.pushMessage({
                            text: 'You have successfully unsubscribed from further emails.',
                            actionText: 'OKAY',
                            timeout: 2000
                        });
                    }
                }.bind(this)
            });
        } else {
            g.XHR_PrivateAPI({
                type: 'POST',
                path: '/feedback/subscribe',
                data: { email: g.vmMain.getUserEmail() },
                requireAuth: true,
                cb: function (xhr) {
                    if (xhr.status !== C.HTTPStatusCode.Success) {
                        g.settings.set(Settings.isUnsubscribed, oldValueUnsub);

                        g.toasts.pushMessage({
                            text: 'There was an error updating your subscription status. Please retry or contact support@moo.do.',
                            actionText: 'RETRY',
                            timeout: 8000,
                            action: function () {
                                this.onUnsubscribedChanged(value);
                            }.bind(this)
                        });
                    } else {
                        g.toasts.pushMessage({
                            text: 'Success!',
                            actionText: 'OKAY',
                            timeout: 2000
                        });
                    }
                }.bind(this)
            });
        }
    },
    onInvoiceEnabledChanged: function onInvoiceEnabledChanged(value) {
        this._onInvoiceChanged(value, g.settings.get(Settings.invoiceEmail), g.settings.get(Settings.invoiceCompany), g.settings.get(Settings.invoiceAddress));
    },
    onInvoiceEmailChanged: function onInvoiceEmailChanged(value) {
        clearTimeout(this._invoiceEmailTimeout);

        this._invoiceEmailTimeout = setTimeout(function () {
            this._onInvoiceChanged(g.settings.get(Settings.monthlyInvoice), value, g.settings.get(Settings.invoiceCompany), g.settings.get(Settings.invoiceAddress));

            this._invoiceEmailTimeout = undefined;
        }.bind(this), 3000);
    },
    onInvoiceCompanyChanged: function onInvoiceCompanyChanged(value) {
        clearTimeout(this._invoiceCompanyTimeout);

        this._invoiceCompanyTimeout = setTimeout(function () {
            this._onInvoiceChanged(g.settings.get(Settings.monthlyInvoice), g.settings.get(Settings.invoiceEmail), value, g.settings.get(Settings.invoiceAddress));

            this._invoiceCompanyTimeout = undefined;
        }.bind(this), 3000);
    },
    onInvoiceAddressChanged: function onInvoiceAddressChanged(value) {
        clearTimeout(this._invoiceAddressTimeout);

        this._invoiceAddressTimeout = setTimeout(function () {
            this._onInvoiceChanged(g.settings.get(Settings.monthlyInvoice), g.settings.get(Settings.invoiceEmail), g.settings.get(Settings.invoiceCompany), value);

            this._invoiceAddressTimeout = undefined;
        }.bind(this), 3000);
    },
    _onInvoiceChanged: function _onInvoiceChanged(isEnabled, invoiceEmail, invoiceCompany, invoiceAddress) {
        var oldValueEnabled = g.settings.get(Settings.monthlyInvoice);
        var oldValueEmail = g.settings.get(Settings.invoiceEmail);
        var oldValueCompany = g.settings.get(Settings.invoiceCompany);
        var oldValueAddress = g.settings.get(Settings.invoiceAddress);

        g.settings.set(Settings.monthlyInvoice, isEnabled);
        g.settings.set(Settings.invoiceEmail, invoiceEmail);
        g.settings.set(Settings.invoiceCompany, invoiceCompany);
        g.settings.set(Settings.invoiceAddress, invoiceAddress);

        g.sendEvent('InvoiceChanged', isEnabled);

        var sendValue = isEnabled ? 1 : 0;

        g.XHR_PrivateAPI({
            type: 'POST',
            path: '/billing/invoice',
            data: {
                isEnabled: sendValue,
                invoiceEmail: invoiceEmail,
                invoiceCompany: invoiceCompany,
                invoiceAddress: invoiceAddress
            },
            requireAuth: true,
            cb: function (xhr) {
                if (xhr.status !== C.HTTPStatusCode.Success) {
                    g.settings.set(Settings.monthlyInvoice, oldValueEnabled);
                    g.settings.set(Settings.invoiceEmail, oldValueEmail);
                    g.settings.set(Settings.invoiceCompany, oldValueCompany);
                    g.settings.set(Settings.invoiceAddress, oldValueAddress);

                    g.toasts.pushMessage({
                        text: 'There was an error updating your invoice status on the server. Please retry in a minute.',
                        actionText: 'RETRY',
                        timeout: 8000,
                        action: function () {
                            this._onInvoiceChanged(isEnabled, invoiceEmail, invoiceCompany, invoiceAddress);
                        }.bind(this)
                    });
                } else {
                    g.toasts.pushMessage({
                        text: 'Your invoice information was successfully updated.',
                        actionText: 'OKAY',
                        timeout: 2000
                    });
                }

                this.updateState();
            }.bind(this)
        });
    },
