import g from '~/globals';
import C from 'Constants';
import platform from 'platform';
import gdata from 'gdata';
import React from 'react';
import MooReact from 'MooReact';
import FancyLoader from 'Components/FancyLoader';
import FancyButton from 'Components/FancyButton';

var ReactModalSettingsEarly = MooReact.createClass({
    displayName: 'ReactModalSettingsEarly',
    componentDisplay: C.ComponentDisplay.Desktop,
    // propTypes:
    // {
    // },
    init: function init() {
        this.createObservables({
            isSendingFeedback: false
        });
    },
    openSection: function openSection(section) {
        this.props.openSection(section);
    },
    onClickDiscard: function onClickDiscard() {
        this.refs.feedback.value = '';
        this.refs.feedback.focus();
    },
    onClickSend: function onClickSend(e, cb) {
        var desc = this.refs.feedback.value;

        if (desc && desc.length > 2) {
            this.isSendingFeedback.set(true);
            var name = 'Moo.do Feedback';

            g.sendFeedback(name, desc, function () {
                this.refs.feedback.value = '';
                this.isSendingFeedback.set(false);
                if (cb && g.isFunction(cb)) {
                    cb();
                }
            }.bind(this));

            return true;
        }
    },
    render: function render() {
        var BM = g.billingManager,
            props = this.props,
            state = this.state;

        return React.createElement(
            'div',
            { key: 'content', className: 'new-modal-group' },
            React.createElement(
                'div',
                { className: 'new-modal-title' },
                React.createElement(
                    'div',
                    { className: 'title' },
                    'Send Feedback'
                ),
                React.createElement(
                    'div',
                    { className: 'subtext middle' },
                    'Send the developers bug reports, feedback, or suggestions. Your feedback is very helpful in planning what to do next.'
                )
            ),
            React.createElement(
                'div',
                { className: 'earlyAccessFeedback' },
                React.createElement('textarea', { className: 'feedbackTextArea c-background c-foreground', ref: 'feedback', style: { marginBottom: 16, color: state.isSendingFeedback ? '#aaa' : null } }),
                state.isSendingFeedback && React.createElement(FancyLoader, { top: 100 }),
                React.createElement(
                    'div',
                    { style: { textAlign: 'right' } },
                    React.createElement(
                        FancyButton,
                        { raised: true, onClick: this.onClickDiscard },
                        'DISCARD'
                    ),
                    React.createElement(
                        FancyButton,
                        { raised: true, primary: true, marginLeft: 16, onClick: this.onClickSend },
                        'SEND'
                    )
                )
            )
        );
    }
});

export default ReactModalSettingsEarly;