import React from 'react';
import util from '~/util';
import g from '~/globals';
import C from 'Constants';
import MooReact from 'MooReact';
import PluginSettings from 'PluginSettings';
import DisplaySettings from 'DisplaySettings';
import AccountManager from 'AccountManager';
import FancyCheckbox from 'Components/FancyCheckbox';
import FancyButton from 'Components/FancyButton';
import FancyLoader from 'Components/FancyLoader';
import FancyModal from 'Components/FancyModal';

var WelcomePage = {
    Connecting: 1,
    EnableTrial: 2,
    PluginSelect: 3,
    ThemeSelect: 4,
    ImportData: 5
};

var ReactModalWelcome = MooReact.createClass({
    displayName: 'ReactModalWelcome',
    componentDisplay: C.ComponentDisplay.Desktop,
    init: function init() {
        var defaultPage = WelcomePage.Connecting;

        var alreadyPremium = util.getURLParam('ispremium');
        if (alreadyPremium) {
            defaultPage = WelcomePage.PluginSelect;
        }

        this.createObservables({
            page: defaultPage,
            isConnected: false,
            colorTheme: C.ColorTheme.Light
        });

        AccountManager.onConnected(function () {
            this.isConnected.set(true);

            if (!alreadyPremium) {
                if (g.billingManager.hasPremiumFeatures()) {
                    this.page.set(WelcomePage.PluginSelect);
                } else {
                    this.page.set(WelcomePage.EnableTrial);
                }
            }
        }.bind(this));

        util.removeURLParam('ispremium', /*noReload*/true);
    },
    onClickEnable: function onClickEnable() {
        var _this = this;

        try{g.PAssert(1787,this.state.isConnected, 'Must always be connected to the server before enabling');}catch(PERR){g.reportError(PERR);}

        var page = this.state.page,
            plugins = [PluginID.Contacts, PluginID.GCalendar, PluginID.Gmail, PluginID.Drive],
            pluginsFiltered = page === WelcomePage.EnableTrial ? plugins : plugins.filter(function (id) {
            return this.refs[id].getValue();
        }.bind(this));

        var enabledPlugins = pluginsFiltered.map(function (id) {
            if (id === PluginID.Drive) {
                return { id: id, shared: page === WelcomePage.EnableTrial || this.refs.DriveShared.getValue() };
            } else {
                return { id: id };
            }
        }.bind(this));

        if (DEBUG && g.isDemoMode()) {
            this.page.set(WelcomePage.ThemeSelect);
        }

        PluginSettings.enablePlugins(enabledPlugins, /*force*/false, function (success) {
            if (success && _this.refs.modal) {
                if (!g.billingManager.hasPremiumFeatures()) {
                    if (pluginsFiltered.indexOf(PluginID.Drive) >= 0 || pluginsFiltered.indexOf(PluginID.GCalendar) >= 0) {
                        g.billingManager.startTrial();
                    }
                }

                _this.page.set(WelcomePage.ThemeSelect);
            }
        });
    },
    onClickChoose: function onClickChoose() {
        this.page.set(WelcomePage.PluginSelect);
    },
    onChangeDrive: function onChangeDrive(value) {
        this.onChangeFeature(value);

        if (!value) {
            // Refactor: I usually say it's bad to set values on components but I
            // couldn't think of a cleaner way to do this
            this.refs.DriveShared.setValue(false);
        }
    },
    onChangeDriveShared: function onChangeDriveShared(value) {
        this.onChangeFeature(value);

        if (value) {
            this.refs[PluginID.Drive].setValue(true);
        }
    },
    onChangeFree: function onChangeFree(value) {
        this.refs[PluginID.Contacts].setValue(value);
        this.refs[PluginID.Gmail].setValue(value);
    },
    onChangePremium: function onChangePremium(value) {
        this.refs[PluginID.GCalendar].setValue(value);
        this.refs[PluginID.Drive].setValue(value);
        this.refs.DriveShared.setValue(value);
    },
    checkTier: function checkTier(ref, arr) {
        var _loopIt = 0;

        var value = 0;

        for (var i = 0; i < arr.length; i++) {
            if (_loopIt++ > 5000 && __infLoop && __infLoop(_loopIt)) throw new RangeError();

            if (this.refs[arr[i]].getValue()) value++;else value--;
        }

        if (value === arr.length) {
            ref.setValue(true);
        }
        if (value === -arr.length) {
            ref.setValue(false);
        }
    },
    onChangeFeature: function onChangeFeature(value) {
        this.checkTier(this.refs.free, [PluginID.Contacts, PluginID.Gmail]);
        this.checkTier(this.refs.premium, [PluginID.GCalendar, PluginID.Drive, 'DriveShared']);
    },
    setColorTheme: function setColorTheme(theme) {
        this.colorTheme.set(theme);

        if (theme) {
            DisplaySettings.changeColorTheme(theme);
        }
    },
    onClickDisplaySettings: function onClickDisplaySettings() {
        this.page.set(WelcomePage.ImportData);
    },
    onClickGetStarted: function onClickGetStarted() {
        this.refs.modal.close();
    },
    onClickImport: function onClickImport(importer) {
        this.refs.modal.close();
        g.menus.openModalNew(Modals.ImportData, { importer: importer });
    },
    renderPage: function renderPage(page) {
        switch (page) {
            case WelcomePage.Connecting:
                return this.renderPageConnecting();
            case WelcomePage.EnableTrial:
                return this.renderPageEnableTrial();
            case WelcomePage.PluginSelect:
                return this.renderPagePluginSelect();
            case WelcomePage.ThemeSelect:
                return this.renderPageThemeSelect();
            case WelcomePage.ImportData:
                return this.renderPageImportData();
                break;
            default:
                try{g.PAssert(1788,false, 'Invalid intro page requested: ' + page);}catch(PERR){g.reportError(PERR);}
                break;
        }
    },
    renderPageConnecting: function renderPageConnecting() {
        return React.createElement(
            'div',
            { key: 'content' },
            React.createElement(
                'div',
                { className: 'setupTitle' },
                'Welcome to ',
                React.createElement('span', { className: 'logoPseudo' }),
                '!'
            ),
            React.createElement(
                'div',
                { className: 'flexbox flex-center', style: { margin: '64px 0' } },
                React.createElement(
                    FancyLoader,
                    { center: true },
                    'Connecting to server...'
                )
            )
        );
    },
    renderPageEnableTrial: function renderPageEnableTrial() {
        var buttonText = this.state.isConnected ? 'NEXT' : 'Connecting...';

        return React.createElement(
            'div',
            { key: 'content' },
            React.createElement(
                'div',
                { className: 'setupTitle' },
                'Welcome to ',
                React.createElement('span', { className: 'logoPseudo' }),
                '!'
            ),
            React.createElement(
                'div',
                { className: 'setupFeatures featureCheckboxes' },
                React.createElement(
                    'div',
                    { className: 'setupPage1Subtitle' },
                    'Start your ',
                    C.TrialDuration,
                    ' day free trial of ',
                    React.createElement(
                        'b',
                        null,
                        'Moo.do Premium'
                    ),
                    ' and enable syncing with:'
                ),
                React.createElement(
                    'div',
                    { className: 'featureTier flexbox c-border' },
                    React.createElement(
                        'div',
                        { className: 'setupFeature setupFeatureRow f1' },
                        React.createElement('img', { src: '/img/plugin-gcal.png' }),
                        React.createElement(
                            'div',
                            null,
                            'Google Calendar'
                        )
                    ),
                    React.createElement(
                        'div',
                        { className: 'setupFeature setupFeatureRow f1' },
                        React.createElement('img', { src: '/img/plugin-gmail.png' }),
                        React.createElement(
                            'div',
                            null,
                            'Gmail'
                        )
                    ),
                    React.createElement(
                        'div',
                        { className: 'setupFeature setupFeatureRow f1' },
                        React.createElement('img', { src: '/img/plugin-gdrive.png' }),
                        React.createElement(
                            'div',
                            null,
                            'Google Drive'
                        )
                    ),
                    React.createElement(
                        'div',
                        { className: 'setupFeature setupFeatureRow f1' },
                        React.createElement('i', { className: 'icon-group' }),
                        React.createElement(
                            'div',
                            null,
                            'Google Contacts'
                        )
                    )
                ),
                React.createElement(
                    'div',
                    { className: 'setupSubtext' },
                    React.createElement(
                        'b',
                        null,
                        'Moo.do'
                    ),
                    ' connects with your Google services to integrate everything together. ',
                    React.createElement(
                        'b',
                        null,
                        'Moo.do'
                    ),
                    ' data is stored in your personal Google Drive account and is never sent to any other server. Nobody can access any of your data. See our ',
                    React.createElement(
                        'span',
                        { className: 'link' },
                        'Privacy Policy'
                    ),
                    ' for more details.'
                ),
                React.createElement(
                    'div',
                    { className: 'setupButtons flexbox' },
                    React.createElement(
                        FancyButton,
                        { raised: true, onClick: this.onClickChoose },
                        'No thanks, I\'ll choose what to enable'
                    ),
                    React.createElement(
                        FancyButton,
                        { enabled: this.state.isConnected, raised: true, primary: true, onClick: this.onClickEnable },
                        buttonText
                    )
                )
            )
        );
    },
    renderPagePluginSelect: function renderPagePluginSelect() {
        var buttonText = this.state.isConnected ? 'NEXT' : 'Connecting...';

        return React.createElement(
            'div',
            { key: 'content', className: 'pageConnect' },
            React.createElement(
                'div',
                { className: 'setupTitle' },
                'Connect ',
                React.createElement('span', { className: 'logoPseudo' }),
                ' with...'
            ),
            React.createElement(
                'div',
                { className: 'setupFeatures featureCheckboxes' },
                React.createElement(
                    'div',
                    { className: 'featureTier c-border' },
                    React.createElement(
                        'div',
                        { className: 'tierTitleWrapper' },
                        React.createElement(
                            FancyCheckbox,
                            { ref: 'free', className: 'tierTitle c-background', checked: true, onChange: this.onChangeFree },
                            'Free Features'
                        )
                    ),
                    React.createElement(
                        'div',
                        { className: 'flexbox' },
                        React.createElement(
                            'div',
                            { className: 'setupFeatureHalf' },
                            React.createElement(
                                FancyCheckbox,
                                { ref: PluginID.Contacts, checked: true, onChange: this.onChangeFeature },
                                React.createElement('i', { className: 'icon-group' }),
                                'Google Contacts'
                            )
                        ),
                        React.createElement(
                            'div',
                            { className: 'setupFeatureHalf' },
                            React.createElement(
                                FancyCheckbox,
                                { ref: PluginID.Gmail, checked: true, onChange: this.onChangeFeature },
                                React.createElement('img', { src: '/img/plugin-gmail.png' }),
                                'Gmail'
                            )
                        )
                    )
                ),
                React.createElement(
                    'div',
                    { className: 'featureTier c-border' },
                    React.createElement(
                        'div',
                        { className: 'tierTitleWrapper' },
                        React.createElement(
                            FancyCheckbox,
                            { ref: 'premium', className: 'tierTitle c-background', checked: true, onChange: this.onChangePremium },
                            'Premium Features'
                        )
                    ),
                    !g.billingManager.hasPremiumFeatures() && React.createElement(
                        'div',
                        { className: 'featureTierDesc c-foreground-light' },
                        'Enabling Premium features will start a free ' + C.TrialDuration + ' day trial of Moo.do Premium.'
                    ),
                    React.createElement(
                        'div',
                        { className: 'flexbox' },
                        React.createElement(
                            'div',
                            { className: 'setupFeature setupFeatureHalf' },
                            React.createElement(
                                FancyCheckbox,
                                { ref: PluginID.GCalendar, checked: true, onChange: this.onChangeFeature },
                                React.createElement('img', { src: '/img/plugin-gcal.png' }),
                                'Google Calendar'
                            )
                        ),
                        React.createElement(
                            'div',
                            { className: 'setupFeature setupFeatureHalf' },
                            React.createElement(
                                FancyCheckbox,
                                { ref: PluginID.Drive, checked: true, onChange: this.onChangeDrive },
                                React.createElement('img', { src: '/img/plugin-gdrive.png' }),
                                'Google Drive'
                            ),
                            React.createElement(
                                FancyCheckbox,
                                { ref: 'DriveShared', checked: true, className: 'subcheckbox', onChange: this.onChangeDriveShared },
                                'Share files with collaborators'
                            )
                        )
                    )
                ),
                React.createElement(
                    'div',
                    { className: 'setupSubtext' },
                    React.createElement(
                        'b',
                        null,
                        'Moo.do'
                    ),
                    ' connects with your Google services to integrate everything together. ',
                    React.createElement(
                        'b',
                        null,
                        'Moo.do'
                    ),
                    ' data is stored in your personal Google Drive account and is never sent to any other server. Nobody can access any of your data. See our ',
                    React.createElement(
                        'span',
                        { className: 'link' },
                        'Privacy Policy'
                    ),
                    ' for more details.'
                ),
                React.createElement(
                    'div',
                    { className: 'setupButtons center', style: { marginTop: 8 } },
                    React.createElement(
                        FancyButton,
                        { enabled: this.state.isConnected, raised: true, primary: true, onClick: this.onClickEnable },
                        buttonText
                    )
                )
            )
        );
    },
    renderPageThemeSelect: function renderPageThemeSelect() {
        var state = this.state,
            color = state.colorTheme;

        return React.createElement(
            'div',
            { key: 'content' },
            React.createElement(
                'div',
                { className: 'setupTitle' },
                'What\'s your style?'
            ),
            React.createElement(
                'div',
                { className: 'flexbox flex-center', style: { margin: '24px 0' } },
                React.createElement(
                    'div',
                    { className: 'setupStyle', onClick: this.setColorTheme.bind(this, C.ColorTheme.Light) },
                    React.createElement(
                        FancyCheckbox,
                        { radio: true, ref: 'checkLight', checked: color === C.ColorTheme.Light },
                        'Light'
                    )
                ),
                React.createElement(
                    'div',
                    { className: 'setupStyle', onClick: this.setColorTheme.bind(this, C.ColorTheme.Dark) },
                    React.createElement(
                        FancyCheckbox,
                        { radio: true, ref: 'checkDark', checked: color === C.ColorTheme.Dark },
                        'Dark'
                    )
                )
            ),
            React.createElement(
                'div',
                { className: 'setupSubtext center' },
                'Don\'t worry! You can always change your style later.'
            ),
            React.createElement(
                'div',
                { className: 'setupButtons center', style: { marginTop: 8 } },
                React.createElement(
                    FancyButton,
                    { raised: true, primary: true, onClick: this.onClickDisplaySettings },
                    'NEXT'
                )
            )
        );
    },
    renderPageImportData: function renderPageImportData() {
        var state = this.state,
            listImporters = ['Wunderlist', 'Workflowy', 'Trello', 'Google Tasks', 'Todoist', 'IQTell'];

        return React.createElement(
            'div',
            { key: 'content' },
            React.createElement(
                'div',
                { className: 'setupTitle' },
                'Import from another app'
            ),
            React.createElement(
                'div',
                { className: 'welcomeImporters' },
                listImporters.map(function (name, i) {
                    return React.createElement(
                        FancyButton,
                        { key: i, heavy: true, onClick: this.onClickImport.bind(this, name) },
                        name
                    );
                }.bind(this))
            ),
            React.createElement(
                'div',
                { className: 'setupSubtext center', style: { marginTop: 16 } },
                'Don\'t worry! You can always import your data later.'
            ),
            React.createElement(
                'div',
                { className: 'setupButtons center', style: { marginTop: 8 } },
                React.createElement(
                    FancyButton,
                    { raised: true, primary: true, onClick: this.onClickGetStarted },
                    'FINISH'
                )
            )
        );
    },
    render: function render() {
        var page = this.state.page;

        return React.createElement(
            FancyModal,
            { id: 'modalWelcome', width: 720, minWidth: 600, modalID: Modals.Welcome, closeButton: false, backdropClose: false, ref: 'modal' },
            this.renderPage(this.state.page)
        );
    }
});

export default ReactModalWelcome;