import React from 'react';
import MooReact from 'MooReact';
import C from 'Constants';

var FancyFocusGuard = MooReact.createClass({
    displayName: 'FancyFocusGuard',
    componentDisplay: C.ComponentDisplay.Desktop,
    onFocus: function onFocus() {
        var target = this.props.component.refs[this.props.target];
        if (target) {
            target.focus();
        }
    },
    render: function render() {
        return React.createElement('input', { className: 'fancyFocusGuard', onFocus: this.onFocus, tabIndex: this.props.tabIndex });
    }
});

export default FancyFocusGuard;