import g from '~/globals';
import C from 'Constants';
import React from 'react';
import MooReact from 'MooReact';
import FancyContextMenu from 'Components/FancyContextMenu';
import FancyContextMenuItem from 'Components/FancyContextMenuItem';
import FancyContextMenuHeader from 'Components/FancyContextMenuHeader';

var ReactContextMenuGmailSort = MooReact.createClass({
    displayName: 'ReactContextMenuGmailSort',
    componentDisplay: C.ComponentDisplay.Desktop,
    setViewMode: function setViewMode(viewMode) {
        this.props.onSelected(viewMode);
    },
    render: function render() {
        var pane = this.props.pane;

        return React.createElement(
            FancyContextMenu,
            { key: 'contextMenuGmailSort', id: 'contextMenuGmailSort', element: this.props.element },
            React.createElement(
                FancyContextMenuHeader,
                null,
                'Sort by'
            ),
            React.createElement(
                FancyContextMenuItem,
                { icon: 'icon-access_time', onClick: this.setViewMode.bind(this, C.GmailViewMode.Date) },
                C.GmailViewMode.Date
            ),
            React.createElement(
                FancyContextMenuItem,
                { icon: 'icon-exclamation', onClick: this.setViewMode.bind(this, C.GmailViewMode.Priority) },
                C.GmailViewMode.Priority
            )
        );
    }
});

export default ReactContextMenuGmailSort;