import g from '~/globals';
import C from 'Constants';
import React from 'react';
import MooReact from 'MooReact';
import platform from 'platform';
import Hotkeys from 'Hotkeys';

var FancyTextArea = MooReact.createClass({
    displayName: 'FancyTextArea',
    componentDisplay: C.ComponentDisplay.Everywhere,
    // propTypes:
    // {
    //     selectionStart: React.PropTypes.number,
    //     selectionEnd: React.PropTypes.number,
    //     spellCheck: React.PropTypes.bool,
    //     focused: React.PropTypes.bool,
    //     value: React.PropTypes.string,
    //     onFocus: React.PropTypes.func,
    //     onBlur: React.PropTypes.func,
    //     isReadOnly: React.PropTypes.bool
    // },
    init: function init() {
        this.createObservables({
            isFocused: this.props.focused || false
        });
    },
    componentDidMount: function componentDidMount() {
        var _this = this;

        var inputBox = this.refs.input;

        if (this.props.value) {
            inputBox.value = this.props.value;
        }

        if (this.props.focused) {
            requestAnimationFrame(function () {
                if (inputBox) {
                    inputBox.focus();

                    if (_this.props.selectionStart !== undefined) {
                        inputBox.selectionStart = _this.props.selectionStart;
                    }

                    if (_this.props.selectionEnd !== undefined) {
                        inputBox.selectionEnd = _this.props.selectionEnd;
                    }

                    inputBox.scrollTop = 0;
                }
            });
        }
    },
    onUnmount: function onUnmount() {
        if (this.isFocused.get()) {
            this.onBlur();
        }
    },
    focus: function focus() {
        this.refs.input.focus();
    },
    blur: function blur() {
        this.refs.input.blur();
    },
    getValue: function getValue() {
        return this.refs.input.value;
    },
    setValue: function setValue(value) {
        this.refs.input.value = value;
    },
    getSelectionStart: function getSelectionStart() {
        return this.refs.input.selectionStart;
    },
    getSelectionEnd: function getSelectionEnd() {
        return this.refs.input.selectionEnd;
    },
    onFocus: function onFocus(e) {
        g.vmMain.activeInput.set(this);

        this.isFocused.set(true);

        if (this.props.onEscape) {
            this.__hotkeyEscape = Hotkeys.pushStackEscape(this.handleEscapeKey);
        }

        if (this.props.onFocus) {
            this.props.onFocus(e);
        }

        // if (MOBILE)
        // {
        //     g.keyboardToolbar.show(C.KeyboardToolbarMode.Item);
        // }
    },
    onBlur: function onBlur(e) {
        g.vmMain.activeInput.set(null);

        this.isFocused.set(false);

        if (this.__hotkeyEscape) {
            Hotkeys.popStack(this.__hotkeyEscape);
            this.__hotkeyEscape = undefined;
        }

        if (this.props.onBlur) {
            this.props.onBlur(e);
        }
    },
    onKeyPress: function onKeyPress(e) {
        if (this.props.onKeyPress) {
            this.props.onKeyPress(e);
        }
    },
    handleEscapeKey: function handleEscapeKey() {
        if (this.props.onEscape) {
            this.props.onEscape();
        }
    },
    render: function render() {
        var props = this.props,
            cls = g.classNames('fancyTextarea c-background c-foreground', props.className),
            spellCheck = props.spellCheck ? 'true' : null,
            style = {
            fontSize: props.fontSize,
            height: props.height || 150,
            margin: props.margin !== undefined ? props.margin : '16px 0',
            overflow: props.height <= 34 && 'hidden'
        };

        return React.createElement('textarea', { className: cls, style: style, ref: 'input', readOnly: !!props.isReadOnly, spellCheck: spellCheck, onClick: props.onClick, onChange: props.onChange, onFocus: this.onFocus, onBlur: this.onBlur, onKeyDown: props.onKeyDown, onKeyPress: props.onKeyPress, placeholder: props.placeholder, tabIndex: props.tabIndex });
    }
});

export default FancyTextArea;