import g from '~/globals';
import C from 'Constants';
import Autocomplete from 'Autocomplete';
import React from 'react';
import MooReact from 'MooReact';

var ReactContact = MooReact.createClass({
    displayName: 'ReactContact',
    componentDisplay: C.ComponentDisplay.Everywhere,
    onClickNumber: function onClickNumber(num, e) {
        g.menus.closeContextMenu();
        g.openUrl(num.link, e, true);
    },
    onClickEmail: function onClickEmail(email, e) {
        var plugin = g.vmMain.getDefaultEmailPlugin();

        if (plugin.isActive()) {
            g.menus.closeContextMenu();

            var initData = {
                to: [{ text: '', email: email.address }]
            };

            g.menus.openComposeEmail(plugin, /*threadID*/undefined, /*draftID*/undefined, initData);
        } else {
            g.openUrl(email.link, e, true);
        }
    },
    render: function render() {
        var _this = this,
            contact = this.props.contact;
        return React.createElement(
            'div',
            { className: 'contactPreview' },
            React.createElement(
                'div',
                { className: 'contactName c-border' },
                contact.text
            ),
            contact.phoneNumbers && contact.phoneNumbers.map(function (num) {
                return React.createElement(
                    'div',
                    { className: 'contactRow contactPhone', key: num.number },
                    React.createElement('i', { className: 'contactButton icon-phone c-foreground-super-faded' }),
                    React.createElement(
                        'span',
                        { className: 'contactNumber link', onClick: _this.onClickNumber.bind(_this, num) },
                        num.number
                    ),
                    React.createElement(
                        'span',
                        { className: 'phoneType c-foreground-faded' },
                        num.type
                    )
                );
            }),
            contact.emails && contact.emails.map(function (email) {
                return React.createElement(
                    'div',
                    { className: 'contactRow contactEmail', key: email.address },
                    React.createElement('i', { className: 'icon-mail_outline contactButton c-foreground-super-faded' }),
                    React.createElement(
                        'span',
                        { className: 'contactEmail link', onClick: _this.onClickEmail.bind(_this, email) },
                        email.address
                    )
                );
            })
        );
    }
});

export default ReactContact;