import React from 'react';
import C from 'Constants';
import g from '~/globals';
import MooReact from 'MooReact';
import goog from 'goog';
import FancyInput from 'Components/FancyInput';
import FancyButton from 'Components/FancyButton';
import FancyModal from 'Components/FancyModal';

var ReactModalSharing = MooReact.createClass({
    displayName: 'ReactModalSharing',
    componentDisplay: C.ComponentDisplay.Everywhere,
    init: function init() {
        var props = this.props,
            doc = props.doc;

        this.helpID = 'NewFile';

        this.createObservables({
            isLoading: doc.hasDriveBacking(),
            permissions: [],
            errorName: false,
            errorInvite: false
        });

        this.isCreating = !doc.hasDriveBacking();
        this.isSaving = false;
        this.permissionsRemove = [];

        this.listenToSetting(Settings.closedHelps);

        if (doc.hasDriveBacking()) {
            doc.listPermissions(this.onListPermissions);
        }
    },
    getState: function getState() {
        return {
            shouldShowHelp: g.vmMain.shouldShowHelp(this.helpID)
        };
    },
    onClosing: function onClosing(isSave) {
        if (this.closeCallback) {
            this.closeCallback(isSave);
            this.closeCallback = undefined;
        }
    },
    onListPermissions: function onListPermissions(permissionList) {
        if (permissionList) {
            var _loopIt = 0;

            var permissions = [];
            for (var i = 0; i < permissionList.length; ++i) {
                if (_loopIt++ > 5000 && __infLoop && __infLoop(_loopIt)) throw new RangeError();

                var item = permissionList[i];
                if (item.emailAddress === g.getUserIdentifier()) {
                    // Don't add self
                    continue;
                }

                item.onRemove = this.onClickRemovePermission.bind(this, item);

                if (item.id !== 'anyone') {
                    if (!item.emailAddress) {
                        if (item.role === 'owner') {
                            item.emailAddress = 'Owner';
                        } else {
                            item.emailAddress = '';
                        }
                    }

                    if (item.role === 'owner') {
                        permissions.splice(0, 0, item);
                    } else {
                        permissions.push(item);
                    }
                }
                item.emailDisplay = '(' + item.emailAddress + ')';
            }

            this.permissions.set(permissions, /*notify*/false);
            this.isLoading.set(false);
        }
    },
    onClickRemovePermission: function onClickRemovePermission(user) {
        this.permissionsRemove.push(user);
        this.permissions.remove(user);

        g.sendEvent('Menu', 'SharingRemoveUser');
    },
    onClickGotIt: function onClickGotIt() {
        g.vmMain.closeHelp(this.helpID);
    },
    onClickDelete: function onClickDelete() {
        g.sendEvent('Menu', 'SharingDelete');

        var doc = this.props.doc;

        g.menus.confirm('Are you sure you want to delete the file?', function (result) {
            g.vmMain.docManager.deleteDoc(doc.id, function (success) {
                log('File Delete Response: ', success);

                if (!success) {
                    g.toasts.pushMessage({
                        text: 'Unable to delete file on the remote server.'
                    });
                }
            });

            g.menus.closeModalNew(Modals.ShareDoc, false);
        });
    },
    onClickCancel: function onClickCancel() {
        g.menus.closeModalNew(Modals.ShareDoc, false);

        g.sendEvent('Menu', 'SharingCancel');
    },
    create: function create(callback) {
        var doc = this.props.doc;
        this.justCreated = true;
        this.isSaving = true;

        var title = this.refs.name.getValue().trim();

        doc.rename(title, function (success) {
            try{g.PAssert(1789,success, 'Rename during create must be successful');}catch(PERR){g.reportError(PERR);}
        });

        goog.createAndSetupDocument(doc, /*isDefault*/false, function (response) {
            if (response && !response.error) {
                this.isSaving = false;
                this.isCreating = false;

                this.onClickSave();
            } else {
                this.errorName.set('Error: Unable to create new file.');
            }
        }.bind(this));
    },
    onClickSave: function onClickSave() {
        if (!this.isSaving) {
            var _loopIt2 = 0,
                _loopIt3 = 0;

            g.sendEvent('Menu', 'SharingSave');

            var state = this.state,
                doc = this.props.doc;

            if (this.isSaving && !this.justCreated) {
                return;
            }

            //
            // Check inputs
            //

            if (!this.checkName()) {
                // If the doucment name is not valid, display error.
                return;
            }

            if (this.isCreating) {
                this.create();
                return true;
            }

            // Assume inputs are valid after here

            this.rename();

            var docWasShared = doc.isShared();

            var count = 0;
            var handleResponse = function (success) {
                if (DEBUG) {
                    log('Permission Response: ', success);
                }

                if (!success) {
                    this.errorInvite.set('Some people could not be invited.');
                    this.isSaving = false;
                } else {
                    count--;

                    if (count <= 0) {
                        g.menus.closeModalNew(Modals.ShareDoc, false);

                        if (doc.isOwned() && doc.isOpen()) {
                            g.vmMain.pluginManager.onDocumentShared( /*firstShare*/docWasShared);
                        }
                    }
                }
            }.bind(this);

            var invitees = this.refs.invite.getTokens();

            for (var i = 0; i < invitees.length; i++) {
                if (_loopIt2++ > 5000 && __infLoop && __infLoop(_loopIt2)) throw new RangeError();

                var newPermission = invitees[i];

                count++;

                if (DEBUG) {
                    log('Adding Writer Permission: ', newPermission);
                }

                doc.insertPermission(newPermission.email, handleResponse.bind(this));
            }

            for (var i = 0; i < this.permissionsRemove.length; i++) {
                if (_loopIt3++ > 5000 && __infLoop && __infLoop(_loopIt3)) throw new RangeError();

                var permission = this.permissionsRemove[i];

                count++;

                doc.deletePermission(permission.id, handleResponse.bind(this));
            }

            if (this.errorInvite.get() || this.errorName.get()) {
                return;
            } else if (count === 0) {
                g.menus.closeModalNew(Modals.ShareDoc, true);
            } else {
                this.isSaving = true;
                return true;
            }
        }
    },
    checkName: function checkName() {
        var title = this.refs.name.getValue().trim();

        if (title.length > 0 && title.length < 255) {
            this.errorName.set(false);

            return true;
        }

        this.errorName.set('Error: File name must be >0 and <255 characters.');

        return false;
    },
    rename: function rename() {
        var title = this.refs.name.getValue().trim(),
            doc = this.props.doc;

        if (title !== doc.getTitle()) {
            doc.rename(title, function (success) {
                if (!success) {
                    this.errorName('Error: Unable to update name on remote server.');
                }
            }.bind(this));
        }
    },
    render: function render() {
        var props = this.props,
            state = this.state,
            doc = props.doc,
            shareLink = 'https://www.moo.do/web/#file=' + props.doc.id + '&sinfo=' + props.doc.generateShareInfo();

        return React.createElement(
            FancyModal,
            { id: 'shareDoc', modalID: Modals.ShareDoc, ref: 'modal', title: 'Share File', width: 550, height: 600 },
            React.createElement(
                'div',
                { key: 'content' },
                this.isCreating && state.shouldShowHelp && React.createElement(
                    'div',
                    { className: 'inlineHelp c-bg-faded c-border-heavy' },
                    React.createElement(
                        'div',
                        null,
                        React.createElement(
                            'div',
                            { className: 'inlineHelpTitle' },
                            'HELP'
                        ),
                        React.createElement(
                            'div',
                            null,
                            'A File contains all of your projects and tasks. Each File can be shared with a different set of people to collaborate with your friends or your team. If you are not collaborating with anyone, you likely don\'t want to use this feature.'
                        )
                    ),
                    React.createElement(
                        'div',
                        { className: 'inlineHelpGotIt flexbox flexvert' },
                        React.createElement(
                            FancyButton,
                            { primary: true, onClick: this.onClickGotIt },
                            'GOT IT'
                        )
                    )
                ),
                React.createElement(
                    'div',
                    { className: 'detailsSection flexbox itemContext c-foreground-faded' },
                    React.createElement(
                        'div',
                        { className: 'c-foreground-faded' },
                        'Name'
                    ),
                    React.createElement(FancyInput, { value: props.doc.getTitle(), ref: 'name', focused: this.isCreating })
                ),
                state.errorName && React.createElement(
                    'span',
                    { className: 'error' },
                    state.errorName
                ),
                !this.isCreating && React.createElement(
                    'div',
                    { className: 'detailsSection flexbox itemContext c-foreground-faded' },
                    React.createElement(
                        'div',
                        { className: 'c-foreground-faded' },
                        'Share Link'
                    ),
                    React.createElement(FancyInput, { value: shareLink })
                ),
                !this.isCreating && (state.isLoading || state.permissions.length > 0) && React.createElement(
                    'div',
                    { className: 'modal-group' },
                    React.createElement(
                        'div',
                        { className: 'mat-subheader' },
                        React.createElement('i', { className: 'icon-user' }),
                        ' Who has access'
                    ),
                    React.createElement(
                        'ul',
                        { className: g.classNames('shareAccess c-border', { 'loading': state.isLoading }) },
                        state.permissions.map(function (person) {
                            return React.createElement(
                                'li',
                                { key: person.emailAddress, className: 'sharePerson flexbox' },
                                React.createElement(
                                    'span',
                                    { className: 'shareDetails f1' },
                                    person.role === 'owner' && React.createElement('i', { className: 'icon-star', tooltip: 'Owner' }),
                                    React.createElement(
                                        'span',
                                        null,
                                        person.name
                                    ),
                                    React.createElement(
                                        'span',
                                        { className: 'c-foreground-faded', style: { marginLeft: 16 } },
                                        person.emailAddress
                                    )
                                ),
                                doc.isOwned() && React.createElement(FancyButton, { icon: 'icon-close', onClick: person.onRemove })
                            );
                        })
                    )
                ),
                doc.isOwned() && React.createElement(
                    'div',
                    { className: 'modal-group' },
                    React.createElement(
                        'div',
                        { className: 'mat-subheader' },
                        'Invite to share'
                    ),
                    state.errorInvite && React.createElement(
                        'div',
                        { className: 'error' },
                        state.errorInvite
                    ),
                    React.createElement(FancyInput, { id: 'inputShareInvite', placeholder: 'Email address', autocompleteMode: 'email', ref: 'invite' })
                ),
                !doc.isOwned() && React.createElement(
                    'div',
                    { className: 'modal-group' },
                    React.createElement(
                        'div',
                        { className: 'shareNotOwned gd' },
                        'Only the creator of this file can share it with others. Please contact the creator to share it with others.'
                    )
                ),
                React.createElement('div', { className: 'scrollPad' })
            ),
            React.createElement(
                'div',
                { key: 'buttons' },
                doc.isOwned() && React.createElement(
                    'div',
                    { className: 'flexbox', style: { padding: '0 16px' } },
                    !this.isCreating && React.createElement(
                        FancyButton,
                        { raised: true, cancel: true, id: 'shareDelete', onClick: this.onClickDelete },
                        'DELETE'
                    ),
                    React.createElement('div', { className: 'f1' }),
                    React.createElement(
                        FancyButton,
                        { id: 'shareCancel', raised: true, onClick: this.onClickCancel },
                        'CANCEL'
                    ),
                    React.createElement(
                        FancyButton,
                        { id: 'shareSave', raised: true, primary: true, marginLeft: 16, onClick: this.onClickSave },
                        'SAVE'
                    )
                ),
                !doc.isOwned() && React.createElement(
                    FancyButton,
                    { id: 'shareOkay', raised: true, onClick: this.onClickCancel },
                    'OKAY'
                )
            )
        );
    }
});

export default ReactModalSharing;