import React from 'react';
import C from 'Constants';
import g from '~/globals';
import platform from 'platform';
import MooReact from 'MooReact';
import Multiselect from 'Multiselect';
import FancyButton from 'Components/FancyButton';
import FancyCheckbox from 'Components/FancyCheckbox';
import FancyTextarea from 'Components/FancyTextarea';
import FancyModal from 'Components/FancyModal';

var ReactModalFeedback = MooReact.createClass({
    displayName: 'ReactModalFeedback',
    componentDisplay: C.ComponentDisplay.Everywhere,
    onClickUV: function onClickUV(e) {
        g.openUrl('https://feedback.moo.do/', e);

        g.sendEvent('Menu', 'FeedbackUserVoice');
    },
    onClickTwitter: function onClickTwitter(e) {
        g.openUrl('https://www.twitter.com/MooDoApp', e);

        g.sendEvent('Menu', 'FeedbackTwitter');
    },
    onClickFacebook: function onClickFacebook(e) {
        g.openUrl('https://www.facebook.com/MooDoApp', e);

        g.sendEvent('Menu', 'FeedbackFacebook');
    },
    onClickGooglePlus: function onClickGooglePlus(e) {
        g.openUrl('https://www.google.com/+MooDoApp', e);

        g.sendEvent('Menu', 'FeedbackGoogle');
    },
    onClickCancel: function onClickCancel(e) {
        g.menus.closeModalNew(Modals.ReportBug);
    },
    onClickSend: function onClickSend(e) {
        var desc = this.refs.description.getValue();
        if (desc && desc.length > 2) {
            var name = 'Moo.do Feedback';

            if (this.refs.share.getValue()) {
                var doc = g.vmMain.docManager.getOpenDoc();

                if (doc) {
                    name += ' [DocShared]';

                    desc += '\r\n\r\nShared Document - ' + doc.id;

                    var failureCB = function failureCB() {
                        g.reportError(new Error('Unable to share doc with debug account'));
                    };

                    g.vmMain.remoteAPI().drivePermissionsInsert(doc.id, 'debug@moo.do', /*sendNotificationEmails*/false, g.emptyFn, failureCB);
                }
            }

            g.sendFeedback(name, desc);

            g.menus.closeModalNew(Modals.ReportBug);

            g.toasts.pushMessage({
                text: 'Thanks for taking the time to send us feedback!'
            });

            g.sendEvent('Menu', 'SendFeedback');

            if (MOBILE) {
                Multiselect.reset();
            }
        }
    },
    render: function render() {
        var styleButtons = {
            paddingBottom: platform.getKBSize()
        };

        return React.createElement(
            FancyModal,
            { id: 'reportBug', modalID: Modals.ReportBug, ref: 'modal', title: 'Contact Support', width: 650, height: 650 },
            React.createElement(
                'div',
                { key: 'content' },
                React.createElement(
                    'div',
                    { className: 'c-foreground-light' },
                    'If you\'re requesting a feature, it\'s often easier to vote it up (or add it) on our UserVoice page.'
                ),
                React.createElement(
                    'div',
                    { className: 'center' },
                    React.createElement(
                        FancyButton,
                        { className: 'mc', primary: true, onClick: this.onClickUV },
                        'Open UserVoice'
                    )
                ),
                React.createElement('br', null),
                React.createElement(
                    'div',
                    { className: 'c-foreground-light' },
                    'Or talk to us on your favorite social network.'
                ),
                React.createElement(
                    'div',
                    { className: 'feedbackIcons flexbox flex-center' },
                    React.createElement(FancyButton, { width: 50, height: 50, icon: 'icon-twitter', tooltip: '@MooDoApp', onClick: this.onClickTwitter }),
                    React.createElement(FancyButton, { width: 50, height: 50, marginLeft: 16, icon: 'icon-facebook', tooltip: 'MooDoApp', onClick: this.onClickFacebook }),
                    React.createElement(FancyButton, { width: 50, height: 50, marginLeft: 16, icon: 'icon-google-plus', tooltip: '+MooDoApp', onClick: this.onClickGooglePlus })
                ),
                React.createElement(
                    'div',
                    { className: 'mat-subheader c-foreground-light' },
                    'Or send us a message directly.'
                ),
                React.createElement(FancyTextarea, { focused: true, id: 'bugDescription', height: 200, ref: 'description', margin: 0 }),
                React.createElement(
                    'div',
                    { className: 'mt8 c-foreground-light' },
                    'Some error reports may be helped by sharing your file with us. We can diagnose issues in your particular file. Please only share your file with us if we have asked.'
                ),
                React.createElement('br', null),
                React.createElement(
                    FancyCheckbox,
                    { ref: 'share' },
                    'Share file with developers'
                )
            ),
            React.createElement(
                'div',
                { key: 'buttons', style: styleButtons },
                React.createElement(
                    FancyButton,
                    { raised: true, onClick: this.onClickCancel },
                    'CANCEL'
                ),
                React.createElement(
                    FancyButton,
                    { raised: true, primary: true, marginLeft: 16, onClick: this.onClickSend },
                    'SEND'
                )
            )
        );
    }
});

export default ReactModalFeedback;