import g from '~/globals';
import C from 'Constants';
import React from 'react';
import MooReact from 'MooReact';
import tracker from 'tracker';
import FancyContextMenu from 'Components/FancyContextMenu';
import FancyInput from 'Components/FancyInput';
import Sel from 'Sel';

var ReactContextMenuDropZone = MooReact.createClass({
    displayName: 'ReactContextMenuDropZone',
    componentDisplay: C.ComponentDisplay.Desktop,
    init: function init() {
        var pluginDrive = g.vmMain.pluginManager.getPlugin(PluginID.Drive);

        this.createObservables({
            isHovered: false,
            isDriveEnabled: pluginDrive.isEnabled()
        });
    },
    doUpload: function doUpload(files) {
        var _loopIt = 0;

        var plugin = g.vmMain.pluginManager.getPlugin(PluginID.Drive);
        for (var i = 0; i < files.length; i++) {
            if (_loopIt++ > 5000 && __infLoop && __infLoop(_loopIt)) throw new RangeError();

            plugin.handleDropFile(files[i], this.props.pane, /*e*/undefined, this.onUploadComplete.bind(this, /*replaceItem*/i === 0));
        }
        this.refs.menu.close();
    },
    onUploadComplete: function onUploadComplete(replaceItem, text) {
        tracker.beginAction(); // BEGIN TRACKER <----

        var props = this.props,
            item = props.item,
            itemParent = props.itemParent,
            pane = props.pane,
            index = !isNaN(props.insertIndex) ? props.insertIndex : props.paneObject.index + 1;


        if (replaceItem && item) {
            item.setText(text, /*parseImmediate*/true, ChangeType.Local);
        } else {
            var parent = itemParent || item.parent(),
                newItem = pane.createVMLI({ text: text }, { parent: parent, changeType: ChangeType.AllLocal });

            parent.insertItem(newItem, index);
        }

        tracker.endAction(); // END TRACKER <----

        return item;
    },
    onChangeFile: function onChangeFile(e) {
        if (e.target.files && e.target.files.length > 0) {
            this.doUpload(e.target.files);
        }

        return false;
    },
    onDragEnter: function onDragEnter() {
        this.isHovered.set(true);
    },
    onDragLeave: function onDragLeave() {
        this.isHovered.set(false);
    },
    onDrop: function onDrop(e) {
        var data = e.dataTransfer;

        if (data.files && data.files.length > 0) {
            this.doUpload(data.files);
        }
    },
    onClickEnableDrive: function onClickEnableDrive() {
        this.refs.menu.close();
        g.menus.openSettings(C.SettingsSections.Plugins);
    },
    onClick: function onClick() {
        this.refs.picker.click();
    },
    onClickAddUrl: function onClickAddUrl() {
        this.refs.menu.close();

        var props = this.props,
            url = this.refs.inputUrl.getValue();

        props.pane.setIgnoreRestoreScroll();

        if (this.props.item) {
            Sel.selectIndex(this.props.paneObject.index, 0, 0);

            this.props.item.onInsertLink({ url: url }, /*showInline*/g.settings.showImagesInline());
        } else {
            tracker.beginAction(); // BEGIN TRACKER <----

            var parent = props.itemParent,
                newItem = props.pane.createVMLI({ text: g.getDefaultItemText() }, { parent: parent, changeType: ChangeType.AllLocal });

            parent.insertItem(newItem, props.insertIndex);
            Sel.selectIndex(props.insertIndex, newItem.getParsedText().length);
            newItem.onInsertLink({ url: url }, /*showInline*/g.settings.showImagesInline());

            tracker.endAction(); // END TRACKER <----
        }
    },
    render: function render() {
        var props = this.props,
            state = this.state,
            isHovered = this.state.isHovered,
            isImage = props.image,
            clsDrop = g.classNames('uploadDropZone', { 'hovered c-bg-light': isHovered });

        var fileType = isImage ? 'image' : 'file';

        return React.createElement(
            FancyContextMenu,
            { key: 'contextMenuDropZone', id: 'contextMenuDropZone', name: 'ReactContextMenuDropZone', element: props.element, pos: props.pos, ref: 'menu' },
            isImage && React.createElement(FancyInput, { focused: true, placeholder: 'Enter an image address', height: 32, buttonText: 'ADD', onButtonClick: this.onClickAddUrl, ref: 'inputUrl' }),
            isImage && React.createElement(
                'div',
                { className: 'dropOr center c-foreground-faded' },
                'or'
            ),
            state.isDriveEnabled && React.createElement(
                'div',
                { className: clsDrop, onClick: this.onClick, onDragEnter: this.onDragEnter, onDragLeave: this.onDragLeave, onDrop: this.onDrop },
                (isHovered ? 'Drop file(s)' : 'Drag files here or click') + ' to add ' + fileType + '.',
                React.createElement('input', { type: 'file', ref: 'picker', className: 'hidden', multiple: true, onChange: this.onChangeFile })
            ),
            !state.isDriveEnabled && React.createElement(
                'div',
                { className: clsDrop, onClick: this.onClickEnableDrive },
                'Enable the Google Drive plugin to upload files'
            )
        );
    }

});

export default ReactContextMenuDropZone;