import g from '~/globals';
import C from 'Constants';
import React from 'react';
import MooReact from 'MooReact';
import ReactContact from 'ReactContact';
import FancyContextMenu from 'Components/FancyContextMenu';

var ReactContextMenuContactDetails = MooReact.createClass({
    displayName: 'ReactContextMenuContactDetails',
    componentDisplay: C.ComponentDisplay.Everywhere,
    // propTypes:
    // {
    //     contact: React.PropTypes.object.isRequired,
    // },
    onClose: function onClose() {
        g.menus.closeContextMenu('ReactContextMenuContactDetails');
    },
    render: function render() {
        return React.createElement(
            FancyContextMenu,
            { key: 'contextMenuContactDetails', id: 'contextMenuContactDetails', element: this.props.element, position: this.props.position },
            React.createElement(ReactContact, { contact: this.props.contact, onClose: this.onClose })
        );
    }
});

export default ReactContextMenuContactDetails;