import g from '~/globals';
import C from 'Constants';
import React from 'react';
import MooReact from 'MooReact';
import Sel from 'Sel';
import FancyScroller from 'Components/FancyScroller';
import FancyButton from 'Components/FancyButton';

var FancyModal = MooReact.createClass({
    displayName: 'FancyModal',
    componentDisplay: C.ComponentDisplay.Everywhere,
    // propTypes:
    // {
    //     id: React.PropTypes.string.isRequired,
    //     modalID: React.PropTypes.string.isRequired,

    //     width: React.PropTypes.number,
    //     height: React.PropTypes.number,
    //     title: React.PropTypes.string,
    //     backdropClose: React.PropTypes.bool
    // },
    close: function close(callbackValue) {
        if (MOBILE) {
            Sel.closeKeyboard();
        }

        g.menus.closeModalNew(this.props.modalID, callbackValue);
    },
    onCloseButton: function onCloseButton(e) {
        if (this.props.onCloseButton) {
            this.props.onCloseButton();
        } else {
            this.close();
        }
    },
    onEscapeKey: function onEscapeKey() {
        this.close();
    },
    render: function render() {
        var props = this.props,
            sidebarChild = this.getChildByKey('sidebar'),
            contentChild = this.getChildByKey('content'),
            buttonsChild = this.getChildByKey('buttons'),
            showTitle = MOBILE,
            styleModal = {
            maxHeight: !MOBILE && !!props.height ? '85%' : null
        },
            clsModal = g.classNames('fancyModal c-bg-app popup c-shadow-popup', props.className, { 'flexbox flexvert': MOBILE }, { 'small': props.small }, { 'hasSidebar': !!sidebarChild }, { 'hasTitle': showTitle && !!props.title }, { 'hasButtons': !!buttonsChild }),
            clsWrapper = g.classNames('fancyModalWrapper', { 'small flexbox': props.small }),
            clsContent = g.classNames('fancyModalContent c-border c-background f1', contentChild.props.className),
            titleHeight = MOBILE ? g.topBarHeight : 36,
            scroller = React.createElement(
            FancyScroller,
            { className: clsContent, scrollable: props.scrollable },
            contentChild.props.children || contentChild,
            MOBILE && React.createElement(
                'div',
                { className: 'fancyModalButtons' },
                buttonsChild
            )
        );

        if (MOBILE) {
            var confirm, closeButton;

            if (props.confirmIcon) {
                confirm = React.createElement(FancyButton, { className: 'fancyModalMobileConfirm', icon: props.confirmIcon, iconSize: 20, width: titleHeight, height: titleHeight, onClick: props.onConfirm });
            } else if (props.confirmText) {
                confirm = React.createElement(
                    FancyButton,
                    { className: 'fancyModalMobileConfirm', width: props.topButtonWidth, fontSize: 14, height: titleHeight, onClick: props.onConfirm },
                    props.confirmText
                );
            }

            if (props.closeButton !== false) {
                if (g.isString(props.closeButton)) {
                    closeButton = React.createElement(
                        FancyButton,
                        { className: 'fancyModalClose', width: props.topButtonWidth, fontSize: 14, height: titleHeight, onClick: this.onCloseButton },
                        props.closeButton
                    );
                } else {
                    closeButton = React.createElement(FancyButton, { className: 'fancyModalClose', icon: 'icon-close', iconSize: 20, width: titleHeight, height: titleHeight, onClick: this.onCloseButton });
                }
            }

            return React.createElement(
                'span',
                { className: clsWrapper },
                React.createElement(
                    'div',
                    { className: clsModal, style: styleModal, id: props.id },
                    React.createElement(
                        'div',
                        { className: 'flexbox' },
                        closeButton,
                        React.createElement(
                            'div',
                            { className: 'fancyModalTitle f1' },
                            props.title
                        ),
                        confirm
                    ),
                    scroller
                )
            );
        } else {
            if (!props.small) {
                styleModal.width = props.width;
                styleModal.height = props.height;
                styleModal.minWidth = props.minWidth;
            };

            var sidebar;
            if (sidebarChild) {
                var styleSidebar = {
                    width: sidebarChild.props.width || 180
                };
                sidebar = React.createElement(
                    'div',
                    { className: 'fancyModalSidebar', style: styleSidebar },
                    sidebarChild.props.children
                );
            }

            return React.createElement(
                'span',
                { className: clsWrapper },
                React.createElement('div', { className: 'fancyModalBackdrop', onClick: props.backdropClose !== false ? this.close.bind(this, false) : undefined }),
                React.createElement(
                    'div',
                    { className: clsModal, style: styleModal, id: props.id },
                    showTitle && props.closeButton !== false && React.createElement(FancyButton, { className: 'fancyModalClose', icon: 'icon-close', iconSize: 20, width: titleHeight, height: titleHeight, onClick: this.onCloseButton, tooltip: 'Close' }),
                    showTitle && props.title && React.createElement(
                        'div',
                        { className: 'fancyModalTitle' },
                        props.title
                    ),
                    React.createElement(
                        'div',
                        { className: 'fancyModalInside flexbox flex1' },
                        sidebar,
                        React.createElement(
                            'div',
                            { className: 'fancyModalContentArea c-border-light c-background flexbox flexvert f1' },
                            scroller,
                            buttonsChild && React.createElement(
                                'div',
                                { className: g.classNames('fancyModalButtons c-background', { 'border': props.buttonsBorder }) },
                                buttonsChild
                            )
                        )
                    )
                )
            );
        }
    }
});

export default FancyModal;