import React from 'react';
import MooReact from 'MooReact';
import Hotkeys from 'Hotkeys';
import C from 'Constants';
import FancyButton from 'Components/FancyButton';
import FancyModal from 'Components/FancyModal';

var ReactModalAlert = MooReact.createClass({
    displayName: 'ReactModalAlert',
    componentDisplay: C.ComponentDisplay.Everywhere,
    // propTypes:
    // {
    //     text: React.PropTypes.string.isRequired,

    //     actionText: React.PropTypes.string
    // },
    getHotkeys: function getHotkeys() {
        return [Hotkeys.create(KeyCode.Enter, true, C.Primary.Either, C.Shift.Either, this.onClickButton)];
    },
    onClickButton: function onClickButton() {
        this.refs.modal.close();
    },
    render: function render() {
        var text = this.props.actionText || 'OKAY';
        return React.createElement(
            FancyModal,
            { id: 'modalAlert', closeButton: false, small: true, backdropClose: false, scrollable: false, modalID: Modals.Alert, ref: 'modal' },
            React.createElement('div', { key: 'content', dangerouslySetInnerHTML: { __html: this.props.text } }),
            React.createElement(
                'div',
                { key: 'buttons' },
                React.createElement(
                    FancyButton,
                    { primary: true, onClick: this.onClickButton },
                    text
                )
            )
        );
    }
});

export default ReactModalAlert;