import g from '~/globals';
import C from 'Constants';
import React from 'react';
import MooReact from 'MooReact';
import International from 'International';
import FancyContextMenu from 'Components/FancyContextMenu';

var ReactContextMenuEmailDetails = MooReact.createClass({
    displayName: 'ReactContextMenuEmailDetails',
    componentDisplay: C.ComponentDisplay.Desktop,
    _generateParticipantTextExpanded: function _generateParticipantTextExpanded(participant) {
        return participant.text ? React.createElement(
            'span',
            null,
            React.createElement(
                'b',
                null,
                participant.text
            ),
            ' <' + participant.email + '>'
        ) : participant.email;
    },
    renderLabels: function renderLabels() {
        var _loopIt = 0;

        var plugin = g.vmMain.getDefaultEmailPlugin(),
            accountID = this.props.accountID,
            labelIds = this.props.message.labelIds,
            value = [];

        for (var i = 0; i < labelIds.length; i++) {
            if (_loopIt++ > 5000 && __infLoop && __infLoop(_loopIt)) throw new RangeError();

            var l = plugin._getLabelByID(accountID, labelIds[i]);
            value.push(React.createElement(
                'div',
                { key: i },
                l.getName()
            ));
        }

        return this.renderLine('labels', value);
    },
    renderParticipants: function renderParticipants(name) {
        var value = this.props.message[name].map(function (c, i) {
            return React.createElement(
                'div',
                { key: i },
                this._generateParticipantTextExpanded(c)
            );
        }.bind(this));

        return this.renderLine(name, value);
    },
    renderLine: function renderLine(key, value) {
        return React.createElement(
            'div',
            { className: 'emailDetailsLine flexbox' },
            React.createElement(
                'div',
                null,
                key + ':'
            ),
            React.createElement(
                'span',
                { className: 'f1' },
                value
            )
        );
    },
    render: function render() {
        var message = this.props.message,
            d = message.date,
            dateStr = d.toString(International.getFormat(DFormat.DayMonthDateYear)) + ' at ' + d.toString(International.getFormat(DFormat.ShortTime));

        return React.createElement(
            FancyContextMenu,
            { key: 'contextMenuEmailDetails', id: 'contextMenuEmailDetails', element: this.props.element },
            this.renderLine('from', this._generateParticipantTextExpanded(message.from)),
            message.to && this.renderParticipants('to'),
            message.cc && this.renderParticipants('cc'),
            message.bcc && this.renderParticipants('bcc'),
            this.renderLine('date', dateStr),
            this.renderLine('subject', message.subject),
            message.labelIds && this.renderLabels()
        );
    }
});

export default ReactContextMenuEmailDetails;