import g from '~/globals';
import C from 'Constants';
import tracker from 'tracker';
import React from 'react';
import MooReact from 'MooReact';
import FancyContextMenuItem from 'Components/FancyContextMenuItem';

var ReactFile = MooReact.createClass({
    displayName: 'ReactFile',
    componentDisplay: C.ComponentDisplay.Everywhere,
    // propTypes:
    // {
    //     file: React.PropTypes.object.isRequired
    // },
    getState: function getState() {
        var file = this.props.file;

        return {
            isShared: file._isShared,
            title: file._title,
            isOpen: file.isOpen()
        };
    },
    onClickDocumentTitle: function onClickDocumentTitle() {
        var doc = this.props.file,
            picker = g.vmMain.vmPicker;

        picker.openDocument(doc);

        tracker.miscAction({
            type: TrackerType.Misc,
            misc: TrackerMisc.OpenDocument
        });
    },
    onClickDocumentSettings: function onClickDocumentSettings() {
        var doc = this.props.file,
            picker = g.vmMain.vmPicker;

        picker.openSharing(doc);

        g.sendEvent('Menu', 'OpenSharing');

        tracker.miscAction({
            type: TrackerType.Misc,
            misc: TrackerMisc.OpenDocSharing
        });
    },
    render: function render() {
        var state = this.state;

        return React.createElement(
            FancyContextMenuItem,
            { icon: state.isShared ? 'icon-user' : 'icon-settings',
                active: state.isOpen,
                selected: this.props.selected,
                tooltip: 'Open ' + state.title,
                iconTooltip: 'Share / Rename / Delete',
                onClick: this.onClickDocumentTitle,
                onClickIcon: this.onClickDocumentSettings },
            state.title
        );
    }
});

export default ReactFile;