import g from '~/globals';
import C from 'Constants';
import React from 'react';
import MooReact from 'MooReact';
import platform from 'platform';
import FancyCheckbox from 'Components/FancyCheckbox';
import FancyButton from 'Components/FancyButton';

var ReactModalSettingsGeneral = MooReact.createClass({
    displayName: 'ReactModalSettingsGeneral',
    componentDisplay: C.ComponentDisplay.Desktop,
    init: function init() {
        var _this = this;

        this.createObservables({
            isStoragePersisted: false,
            usage: NaN,
            quota: NaN
        });

        platform.isStoragePersisted(function (value) {
            _this.isStoragePersisted.set(value);
        });

        platform.estimateAvailableStorage(function (info) {
            _this.usage.set(info.usage);
            _this.quota.set(info.quota);
        }, function () {
            // Do nothing on failure
        });
    },
    onCacheRatioClick: function onCacheRatioClick() {
        if (this.getCacheRatio() < 0.98) {
            g.settings.set(Settings.cacheSizeRatio, 1);

            g.sendEvent('CacheRatio', 'Changed', 1);

            g.toasts.pushMessage({
                text: 'Success!',
                action: MessageAction.Okay
            });
        }
    },
    onSettingChanged: function onSettingChanged(key, value) {
        if (key === Settings.showFormattingToolbar) {
            value = value ? 1 : -1; // 1 = Show, 0 = Collapsed, -1 = Hide
        }
        g.settings.set(key, value);
    },
    parseFn: function parseFn(val) {
        return parseFloat(val.toFixed(2));
    },
    getCacheRatio: function getCacheRatio() {
        return this.parseFn(g.vmMain.pluginManager.getCacheSizeRatio());
    },
    onPersistClick: function onPersistClick(value) {
        if (!this.state.isStoragePersisted && value) {
            platform.requestStoragePersistence(function () {
                this.isStoragePersisted.set(true);
            }.bind(this), function () {
                this.isStoragePersisted.set(false);
            }.bind(this));
        }
    },
    onChangeDefaultItemPrefix: function onChangeDefaultItemPrefix() {
        g.settings.set(Settings.defaultItemPrefix, this.refs.selectItemPrefix.value);
    },
    render: function render() {
        var allowResetCacheRatio = this.getCacheRatio() < 0.98,
            supportsStoragePersistence = !platform.app && platform.supportsPersistedStorage(),
            supportsEstimatedStorage = platform.supportsEstimatedStorage(),
            defaultItemPrefix = g.settings.get(Settings.defaultItemPrefix),
            showFormattingToolbar = g.settings.get(Settings.showFormattingToolbar) >= 0,
            usage = (this.usage.get() / 1024 / 1024).toFixed(2),
            quota = (this.quota.get() / 1024 / 1024).toFixed(2);

        return React.createElement(
            'div',
            { key: 'content' },
            React.createElement(
                'div',
                { className: 'modal-row' },
                React.createElement(
                    'div',
                    { className: 'settingsName bold ib' },
                    'Default item type:'
                ),
                React.createElement(
                    'select',
                    { defaultValue: defaultItemPrefix, ref: 'selectItemPrefix', onChange: this.onChangeDefaultItemPrefix },
                    React.createElement(
                        'option',
                        { value: '' },
                        'None'
                    ),
                    React.createElement(
                        'option',
                        { value: C.Prefix.Task },
                        'Task'
                    ),
                    React.createElement(
                        'option',
                        { value: C.Prefix.Bullet },
                        'Bullet'
                    )
                ),
                React.createElement(
                    'div',
                    { className: 'modal-subtext c-foreground-faded' },
                    'The default prefix for all new items. If you prefer the bulleted look of an Outliner, choose Bullet. If you prefer a checklist, choose Task. If you prefer the flat look of a document, choose None.'
                )
            ),
            React.createElement(
                'div',
                { className: 'modal-row' },
                React.createElement(
                    FancyCheckbox,
                    { checked: g.settings.get(Settings.showMarkdown), onChange: this.onSettingChanged.bind(this, Settings.showMarkdown) },
                    'Show Markdown formatting'
                ),
                React.createElement(
                    'div',
                    { className: 'modal-subtext c-foreground-faded' },
                    'Show the raw Markdown formatting characters in selected items'
                )
            ),
            React.createElement(
                'div',
                { className: 'modal-row' },
                React.createElement(
                    FancyCheckbox,
                    { checked: g.settings.get(Settings.openEvernoteApp), onChange: this.onSettingChanged.bind(this, Settings.openEvernoteApp) },
                    'Open Evernote links in Evernote app'
                ),
                React.createElement(
                    'div',
                    { className: 'modal-subtext c-foreground-faded' },
                    'If not enabled, Evernote links will open the Evernote website'
                )
            ),
            React.createElement(
                'div',
                { className: 'modal-row' },
                React.createElement(
                    FancyCheckbox,
                    { checked: g.settings.get(Settings.selectFullItems), onChange: this.onSettingChanged.bind(this, Settings.selectFullItems) },
                    'Select full items'
                ),
                React.createElement(
                    'div',
                    { className: 'modal-subtext c-foreground-faded' },
                    'Expand selection to cover the full items when selecting multiple items'
                )
            ),
            React.createElement(
                'div',
                { className: 'modal-row' },
                React.createElement(
                    FancyCheckbox,
                    { checked: showFormattingToolbar, onChange: this.onSettingChanged.bind(this, Settings.showFormattingToolbar) },
                    'Enable Formatting Toolbar'
                ),
                React.createElement(
                    'div',
                    { className: 'modal-subtext c-foreground-faded' },
                    'Show a toolbar with formatting hotkeys when text is selected'
                )
            ),
            React.createElement(
                'div',
                { className: 'modal-row' },
                React.createElement(
                    FancyCheckbox,
                    { checked: g.settings.get(Settings.showImagesInline), onChange: this.onSettingChanged.bind(this, Settings.showImagesInline) },
                    'Show Images Inline'
                ),
                React.createElement(
                    'div',
                    { className: 'modal-subtext c-foreground-faded' },
                    'Show images inline by default when inserting a link to an image, dragging a file from the Drive pane, or uploading an image from your computer. If disabled, it will show a link to the image.'
                )
            ),
            React.createElement('hr', null),
            supportsStoragePersistence && React.createElement(
                'div',
                { className: 'modal-row' },
                React.createElement(
                    FancyCheckbox,
                    { checked: this.state.isStoragePersisted, onChange: this.onPersistClick, canChange: !this.state.isStoragePersisted },
                    'Enable Persistent Storage'
                ),
                React.createElement(
                    'div',
                    { className: 'modal-subtext c-foreground-faded' },
                    'Allow Moo.do to store your data offline without potential for the browser to delete it. This can help Moo.do\'s performance if your disk is low on free-space.'
                )
            ),
            allowResetCacheRatio && React.createElement(
                'div',
                { className: 'modal-row' },
                React.createElement(
                    FancyButton,
                    { raised: true, onClick: this.onCacheRatioClick },
                    'Reset Cache Ratio'
                ),
                React.createElement(
                    'div',
                    { className: 'modal-subtext c-foreground-faded' },
                    'If you have run out of disk space in the past, we may not be efficiently caching your data. If you have resolved any disk space issues, click this button to efficiently cache your data again.'
                )
            ),
            supportsEstimatedStorage && React.createElement(
                'div',
                { className: 'modal-row' },
                React.createElement(
                    'div',
                    { className: 'modal-row-title' },
                    'Estimated disk usage'
                ),
                React.createElement(
                    'div',
                    { className: 'modal-subtext' },
                    React.createElement(
                        'b',
                        null,
                        'Usage: '
                    ),
                    Math.round(usage),
                    ' MB / ',
                    React.createElement(
                        'b',
                        null,
                        'Quota: '
                    ),
                    Math.round(quota),
                    ' MB'
                )
            )
        );
    }
});

export default ReactModalSettingsGeneral;