import g from '~/globals';
import C from 'Constants';
import React from 'react';
import MooReact from 'MooReact';
import FancyModal from 'Components/FancyModal';
import FancyListItem from 'Components/FancyListItem';
import FancyButton from 'Components/FancyButton';
import FancyInput from 'Components/FancyInput';

var dimport;

var ReactModalImport = MooReact.createClass({
    displayName: 'ReactModalImport',
    componentDisplay: C.ComponentDisplay.Desktop,
    // propTypes:
    // {
    // },
    init: function init() {
        var importer = this.props.importer,
            _this = this;

        import( /* webpackChunkName: "dimport" */'dimport').then(function (a) {
            dimport = a.default;
            _this.importer = new dimport();
        });

        if (importer === 'GTasks') {
            importer = 'Google Tasks';
        }

        this.createObservables({
            isImporting: false,
            preloadDisable: 0,
            currentImporter: importer || 'Moo.do Backup',
            importMessage: ''
        });
    },
    getImporterFromName: function getImporterFromName(name) {
        var importFn;

        switch (name) {
            case 'Wunderlist':
                importFn = this.importer.wunderlist;
                break;
            case 'Workflowy':
                importFn = this.importer.workflowy;
                break;
            case 'OPML':
                importFn = this.importer.opml;
                break;
            case 'Moo.do Backup':
                importFn = this.importer.json;
                break;
            case 'Text':
                importFn = this.importer.text;
                break;
            case 'GTasks':
            case 'Google Tasks':
                importFn = this.importer.gtasks;
                break;
            case 'Trello':
                importFn = this.importer.trello;
                break;
            case 'Todoist':
                importFn = this.importer.todoist;
                break;
            case 'Toodledo':
                importFn = this.importer.toodledo;
                break;
            case 'IQTell':
                importFn = this.importer.iqtell;
                break;
            default:
                try{g.PAssert(1785,false, 'Must add valid importer: ', name);}catch(PERR){g.reportError(PERR);}
                break;
        }

        return importFn;
    },
    selectImporter: function selectImporter(name) {
        var importFn = this.getImporterFromName(name);

        this.currentImporter.set(name);
        this.importMessage.set('');
        this.isImporting.set(false);

        if (importFn && importFn.preload && !importFn.isLoaded) {
            this.preloadDisable.set(this.preloadDisable.get() + 1);

            this.importMessage.set('Loading Trello login...');

            importFn.preload(function () {
                this.preloadDisable.set(this.preloadDisable.get() - 1);

                var currentImporter = this.getImporterFromName(this.currentImporter.get());

                if (this.preloadDisable.get() === 0 && currentImporter && currentImporter.preload) {
                    this.importMessage.set('');
                }
            }.bind(this), function (err) {
                this.preloadDisable.set(this.preloadDisable.get() - 1);

                this.importMessage.set('Error loading Trello. Please try again.');
            }.bind(this));
        }
    },
    onImportError: function onImportError(message, error) {
        if (message !== false) {
            if (!message || !g.isString(message)) {
                if (!error) {
                    error = message;
                }

                message = 'Moo.do encountered an error importing your data. The developers have been notified with details of the error.';
            }

            g.reportError(error || message, message);

            this.importMessage.set(message);
        } else {
            this.importMessage.set('');
        }

        this.isImporting.set(false);
    },
    runImport: function runImport(name, loginName, loginPW) {
        if (this.isImporting.get() || this.preloadDisable.get() > 0) {
            debugger;
            return;
        }

        var importFn = this.getImporterFromName(name);

        if (importFn) {
            g.sendEvent('Menu', 'Import' + name);

            this.isImporting.set(true);

            this.importMessage.set('Importing...');

            try {
                setTimeout(function () {
                    importFn.login(loginName, loginPW, function () {
                        importFn.import(function (data) {
                            importFn.save(data, function () {
                                this.isImporting.set(false);

                                this.importMessage.set('');

                                g.menus.alert({
                                    text: 'Your data has been imported successfully!',
                                    actionText: 'OK',
                                    callback: function callback() {
                                        g.menus.closeModalNew(Modals.ImportData);
                                    }
                                });

                                g.sendEvent('ImportSuccess', name);
                            }.bind(this), this.onImportError);
                        }.bind(this), this.onImportError);
                    }.bind(this), this.onImportError);
                }.bind(this), 0);
            } catch (err) {
                this.onImportError('There was an issue importing your data. Please try again later.', err);
            }
        }
    },
    onTapSelect: function onTapSelect(name) {
        this.selectImporter(name, this.onImportSuccess, this.onImportError);
    },
    whichArea: function whichArea() {
        switch (this.state.currentImporter) {
            case 'Wunderlist':
                return 'OAuthArea';
            case 'Workflowy':
                return 'LoginArea';
            case 'OPML':
                return 'UploadArea';
            case 'Moo.do Backup':
                return 'UploadArea';
            case 'Google Tasks':
            case 'GTasks':
                return 'OAuthArea';
            case 'Trello':
                return 'OAuthArea';
            case 'Todoist':
                return 'OAuthArea';
            case 'Toodledo':
                return 'OAuthArea';
            case 'IQTell':
                return 'UploadAreaIQTell';
            default:
                try{g.PAssert(1786,false, 'Must add valid importer');}catch(PERR){g.reportError(PERR);}
                break;
        }

        return false;
    },
    onClickImport: function onClickImport() {
        if (!this.isImporting.get()) {
            var login = this.refs.email && this.refs.email.getValue();
            var pw = this.refs.password && this.refs.password.getValue();

            this.runImport(this.state.currentImporter, login, pw);
        }
    },
    renderListItem: function renderListItem(name) {
        return React.createElement(
            FancyListItem,
            { selected: this.state.currentImporter === name, onClick: this.onTapSelect.bind(this, name) },
            name
        );
    },
    renderContent: function renderContent() {
        var state = this.state;
        var importer = this.state.currentImporter;
        var inside;

        var area = this.whichArea(importer);

        if (area === 'LoginArea') {
            inside = React.createElement(
                'div',
                null,
                React.createElement(
                    'div',
                    { className: 'importTitle' },
                    'Import data from ',
                    importer
                ),
                React.createElement(FancyInput, { floatingText: 'Email', display: 'block', ref: 'email' }),
                React.createElement(FancyInput, { type: 'password', floatingText: 'Password', display: 'block', ref: 'password' }),
                React.createElement('br', null)
            );
        } else if (area === 'UploadArea') {
            inside = React.createElement(
                'div',
                { id: 'importUploadArea' },
                React.createElement(
                    'div',
                    { className: 'importTitle' },
                    'Upload ',
                    importer,
                    ' file'
                ),
                React.createElement(
                    'div',
                    null,
                    'Select a',
                    importer === 'OPML' ? 'n' : '',
                    ' ',
                    importer,
                    ' file to upload'
                ),
                React.createElement(
                    'form',
                    { name: 'importFileUpload' },
                    React.createElement('input', { id: 'importFileUploadButton', type: 'file', required: 'required', name: 'importFile' })
                )
            );
        } else if (area === 'UploadAreaIQTell') {
            inside = React.createElement(
                'div',
                { id: 'importUploadArea' },
                React.createElement(
                    'div',
                    { className: 'importTitle' },
                    'Upload ',
                    importer,
                    ' files [BETA]'
                ),
                React.createElement(
                    'div',
                    null,
                    'Export each folder from IQTell that you would like to import. You must include either the \'Short Descripton\' or \'Title\' fields for each. Select *all* of the exported files at once to do a single import otherwise data may be missing.'
                ),
                React.createElement(
                    'div',
                    null,
                    '\xA0'
                ),
                React.createElement(
                    'div',
                    null,
                    'Please report any issues to support@moo.do with a description of what you expect vs what you are seeing. Screenshots are very helpful.'
                ),
                React.createElement(
                    'form',
                    { name: 'importFileUpload' },
                    React.createElement('input', { id: 'importFileUploadButton', type: 'file', required: 'required', name: 'importFile', multiple: true })
                )
            );
        } else if (area === 'OAuthArea') {
            inside = React.createElement(
                'div',
                null,
                React.createElement(
                    'div',
                    { className: 'importTitle' },
                    'Import data from ',
                    importer
                ),
                React.createElement(
                    'div',
                    null,
                    'Click to login to your account'
                )
            );
        }

        return React.createElement(
            'div',
            { className: 'importInside' },
            inside,
            React.createElement(
                FancyButton,
                { primary: true, enabled: state.preloadDisable === 0 && !state.isImporting, onClick: this.onClickImport },
                'IMPORT'
            ),
            React.createElement(
                'div',
                { className: 'importMessage' },
                state.importMessage
            )
        );
    },
    render: function render() {
        return React.createElement(
            FancyModal,
            { id: 'modalImport', title: 'Import Data', width: 600, height: 400, modalID: Modals.ImportData },
            React.createElement(
                'div',
                { key: 'sidebar', width: 160 },
                React.createElement(
                    FancyListItem,
                    { header: true },
                    'Import from app'
                ),
                this.renderListItem('Wunderlist'),
                this.renderListItem('Google Tasks'),
                this.renderListItem('Workflowy'),
                this.renderListItem('IQTell'),
                this.renderListItem('Trello'),
                this.renderListItem('Todoist'),
                React.createElement(
                    FancyListItem,
                    { header: true },
                    'Import from data'
                ),
                this.renderListItem('Moo.do Backup'),
                this.renderListItem('OPML')
            ),
            React.createElement(
                'div',
                { key: 'content' },
                this.renderContent()
            )
        );
    }
});

export default ReactModalImport;