import g from '~/globals';
import C from 'Constants';
import React from 'react';
import { CSSTransitionGroup as ReactCSSTransitionGroup } from 'react-transition-group';
import MooReact from 'MooReact';
import DragDrop from 'DragDrop';

// Note: These are all required here becaus they are later created by key
var menusAvailable = {
    ReactAutocomplete: require('ReactAutocomplete'),
    ReactContextMenuOverviewOptions: require('ReactContextMenuOverviewOptions'),
    ReactContextMenuGmailSort: require('ReactContextMenuGmailSort'),
    ReactContextMenuSearch: require('ReactContextMenuSearch'),
    ReactContextMenuNameBoard: require('ReactContextMenuNameBoard'),
    ReactContextMenuSelectGmailAccount: require('ReactContextMenuSelectGmailAccount'),
    ReactContextMenuAddItem: require('ReactContextMenuAddItem'),
    ReactContextMenuCalendars: require('ReactContextMenuCalendars'),
    ReactContextMenuInsertLink: require('ReactContextMenuInsertLink'),
    ReactContextMenuContactDetails: require('ReactContextMenuContactDetails'),
    ReactNotePopup: require('ReactNotePopup'),
    ReactContextMenuDropBucket: require('ReactContextMenuDropBucket'),
    ReactItemDetailsPopup: require('ReactItemDetailsPopup'),
    ReactContextMenuEmailDetails: require('ReactContextMenuEmailDetails'),
    ReactContextMenuItem: require('ReactContextMenuItem'),
    ReactContextMenuOnlineStatus: require('ReactContextMenuOnlineStatus'),
    ReactContextMenuPanes: require('ReactContextMenuPanes'),
    ReactContextMenuAuthorize: require('ReactContextMenuAuthorize'),
    ReactContextMenuDocuments: require('ReactContextMenuDocuments'),
    ReactContextMenuHelp: require('ReactContextMenuHelp'),
    ReactContextMenuShare: require('ReactContextMenuShare'),
    ReactContextMenuOptions: require('ReactContextMenuOptions'),
    ReactContextMenuAddPane: require('ReactContextMenuAddPane'),
    ReactContextMenuUpsell: require('ReactContextMenuUpsell'),
    ReactContextMenuDropZone: require('ReactContextMenuDropZone')
};

var ReactContextMenus = MooReact.createClass({
    displayName: 'ReactContextMenus',
    componentDisplay: C.ComponentDisplay.Everywhere,
    // propTypes:
    // {
    // },
    getState: function getState() {
        return {
            contextMenus: g.menus.contextMenuStack,
            noFade: g.menus.contextMenuNoFade,
            isDragging: DragDrop.isVisible
        };
    },
    onClickBackground: function onClickBackground(e) {
        var top = g.menus.getTopContextMenu();
        if (top) {
            g.menus.closeContextMenu(top);
        }
        g.preventDefault(e);
        g.stopPropagation(e);
    },
    render: function render() {
        var _loopIt = 0;

        var state = this.state;

        var menus = [];
        for (var i = 0; i < state.contextMenus.length; i++) {
            if (_loopIt++ > 5000 && __infLoop && __infLoop(_loopIt)) throw new RangeError();

            var menu = state.contextMenus[i];

            if (!menu.noBackdrop) {
                menus.push(React.createElement('div', { key: 'backdrop_' + i, className: 'contextMenusBackdrop', onMouseDown: this.onClickBackground }));
            }

            if (menu.fn) {
                menus.push(menu.fn(menu));
            } else if (menu.componentName) {
                menu.key = menu.key || menu.componentName;
                menu.id = menu.id || menu.key;

                var component = menusAvailable[menu.componentName].default;
                menus.push(React.createElement(component, menu));
            }
        }

        var classMenus = g.classNames('contextMenusReact', { 'open': menus.length > 0 }, { 'takeClick': menus.length > 0 && !menu.noBackdrop }, { 'none': menus.length === 0 }, { 'hideBackground': state.noFade });

        return React.createElement(
            ReactCSSTransitionGroup,
            { className: classMenus, transitionName: 'contextMenuAnim', transitionEnterTimeout: 150, transitionLeaveTimeout: 200 },
            menus
        );
    }
});

export default ReactContextMenus;