import g from '~/globals';
import C from 'Constants';
import d from 'data';
import React from 'react';
import MooReact from 'MooReact';
import PS from 'PluginSettings';
import FancyButton from 'Components/FancyButton';

var ReactModalSettingsContacts = MooReact.createClass({
    displayName: 'ReactModalSettingsContacts',
    componentDisplay: C.ComponentDisplay.Desktop,
    getState: function getState() {
        var state = {
            contacts: d.contactManager._contacts
        };

        return state;
    },
    render: function render() {
        var state = this.state;

        return React.createElement(
            'div',
            { className: 'settingPluginOptions' },
            React.createElement(
                'div',
                { className: 'subtext' },
                React.createElement(
                    'span',
                    null,
                    'We will not send emails or otherwise communicate with your contacts unless you ask us to.'
                ),
                React.createElement(
                    'span',
                    null,
                    ' If you need instant access to contacts you just added you can sync them by pressing the button below, otherwise your contacts will appear automatically after a few hours.'
                )
            ),
            React.createElement(
                'div',
                { className: 'center' },
                React.createElement(
                    FancyButton,
                    { id: 'refreshContacts', raised: true, primary: true, onClick: PS.onTapRefreshContacts.bind(PS) },
                    'REFRESH CONTACTS'
                ),
                React.createElement(
                    'div',
                    { id: 'numContacts' },
                    state.contacts.length + ' contacts loaded'
                )
            )
        );
    }
});

export default ReactModalSettingsContacts;