import g from '~/globals';
import C from 'Constants';
import d from 'data';
import React from 'react';
import MooReact from 'MooReact';
import PS from 'PluginSettings';
import FancyCheckbox from 'Components/FancyCheckbox';
import FancyButton from 'Components/FancyButton';
import FancyInputNumber from 'Components/FancyInputNumber';

var ReactModalSettingsGmail = MooReact.createClass({
    displayName: 'ReactModalSettingsGmail',
    componentDisplay: C.ComponentDisplay.Desktop,
    init: function init() {
        this.hadGmailSyncUpdated = false;
    },
    onUnmount: function onUnmount() {
        if (this.hadGmailSyncUpdated) {
            var pluginGmail = g.vmMain.pluginManager.getPlugin(PluginID.Gmail);

            if (pluginGmail.isActive()) {
                pluginGmail._forceThreadSync();
            }
        }
    },
    getState: function getState() {
        var state = {
            GmailNumSyncd: PS.GmailNumSyncd,
            GmailNumTotal: PS.GmailNumTotal,
            GmailCacheSize: PS.GmailCacheSize,
            GmailSyncAccounts: PS.GmailSyncAccounts
        };

        return state;
    },
    onSyncTimeChanged: function onSyncTimeChanged(value) {
        g.vmMain.pluginManager.setSetting(PluginID.Gmail, PluginSettingsID.Gmail.SyncTime, value);

        this.hadGmailSyncUpdated = true;
    },
    onScheduleChange: function onScheduleChange(value) {
        g.vmMain.pluginManager.setSetting(PluginID.Gmail, PluginSettingsID.Gmail.ArchiveOnSchedule, value);
    },
    onOrganizeChange: function onOrganizeChange(value) {
        g.vmMain.pluginManager.setSetting(PluginID.Gmail, PluginSettingsID.Gmail.ArchiveOnOrganize, value);
    },
    onSignatureChange: function onSignatureChange(value) {
        g.vmMain.pluginManager.setSetting(PluginID.Gmail, PluginSettingsID.Gmail.InsertSignatureAboveReplies, value);
    },
    onClickSetupAccounts: function onClickSetupAccounts() {
        this.props.openSection(C.SettingsSections.Linked);
    },
    render: function render() {
        var pluginGmail = g.vmMain.pluginManager.getPlugin(PluginID.Gmail);

        var syncTime = pluginGmail.getSetting(PluginSettingsID.Gmail.SyncTime);
        var archiveOnSchedule = pluginGmail.getSetting(PluginSettingsID.Gmail.ArchiveOnSchedule);
        var archiveOnOrganize = pluginGmail.getSetting(PluginSettingsID.Gmail.ArchiveOnOrganize);
        var insertSignatureAboveReplies = pluginGmail.getSetting(PluginSettingsID.Gmail.InsertSignatureAboveReplies);

        var maxValue = g.billingManager.hasPremium() ? C.MaxGmailSyncTime.Premium : C.MaxGmailSyncTime.Free;

        return React.createElement(
            'div',
            { className: 'new-modal-group gmailSettings' },
            React.createElement(
                'div',
                { className: 'new-modal-row new-modal-row-1c center' },
                React.createElement(
                    FancyButton,
                    { raised: true, onClick: this.onClickSetupAccounts },
                    'SETUP ACCOUNTS'
                )
            ),
            React.createElement(
                'div',
                { className: 'new-modal-row new-modal-row-1c' },
                React.createElement(
                    'span',
                    { style: { width: 140 } },
                    'Offline sync days\xA0\xA0'
                ),
                React.createElement(FancyInputNumber, { value: syncTime, minValue: 0, maxValue: maxValue, onChange: this.onSyncTimeChanged }),
                React.createElement(
                    'div',
                    { className: 'subtext' },
                    'The number of days in the past that your email will be saved for offline access.'
                )
            ),
            React.createElement(
                'div',
                { className: 'new-modal-row new-modal-row-1c' },
                React.createElement(
                    FancyCheckbox,
                    { ref: 'archiveOnSchedule', checked: archiveOnSchedule, onChange: this.onScheduleChange },
                    'Archive emails when scheduled'
                ),
                React.createElement(
                    'div',
                    { className: 'subtext' },
                    'If enabled, when you use the \'Schedule\' feature to put an email on your calendar it will be archived.'
                )
            ),
            React.createElement(
                'div',
                { className: 'new-modal-row new-modal-row-1c' },
                React.createElement(
                    FancyCheckbox,
                    { ref: 'archiveOnOrganize', checked: archiveOnOrganize, onChange: this.onOrganizeChange },
                    'Archive emails when organized'
                ),
                React.createElement(
                    'div',
                    { className: 'subtext' },
                    'If enabled, when you drag an email to your outline it will be archived and marked as Moo.do/Handled.'
                )
            ),
            React.createElement(
                'div',
                { className: 'new-modal-row new-modal-row-1c' },
                React.createElement(
                    FancyCheckbox,
                    { ref: 'insertSignatureAboveReplies', checked: insertSignatureAboveReplies, onChange: this.onSignatureChange },
                    'Insert signature before quoted text in replies'
                ),
                React.createElement(
                    'div',
                    { className: 'subtext' },
                    'If enabled, your signature will be inserted above the quoted text in replies and will remove the \'--\' line that precedes it.'
                )
            )
        );
    }
});

export default ReactModalSettingsGmail;