import g from '~/globals';
import C from 'Constants';
import platform from 'platform';
import React from 'react';
import MooReact from 'MooReact';
import PS from 'PluginSettings';
import AccountManager from 'AccountManager';
import ReactInviteSection from 'ReactInviteSection';
import ReactModalSettingsGeneral from 'ReactModalSettingsGeneral';
import ReactModalSettingsPlugins from 'ReactModalSettingsPlugins';
import ReactModalSettingsAccounts from 'ReactModalSettingsAccounts';
import ReactModalSettingsEarly from 'ReactModalSettingsEarly';
import ReactModalSettingsLinked from 'ReactModalSettingsLinked';
import ReactModalSettingsGCal from 'ReactModalSettingsGCal';
import ReactModalSettingsContacts from 'ReactModalSettingsContacts';
import ReactModalSettingsGmail from 'ReactModalSettingsGmail';
import ReactModalSettingsAgenda from 'ReactModalSettingsAgenda';
import ReactModalSettingsDates from 'ReactModalSettingsDates';
import ReactModalSettingsBackup from 'ReactModalSettingsBackup';
import FancyModal from 'Components/FancyModal';
import FancyListItem from 'Components/FancyListItem';

var ReactModalSettings = MooReact.createClass({
    displayName: 'ReactModalSettings',
    componentDisplay: C.ComponentDisplay.Desktop,
    init: function init() {
        this.createObservables({
            sectionOpened: this.props.section || C.SettingsSections.General
        });

        PS.refresh();

        PS.getPluginEnabledObservable(PluginID.GCalendar).listen(this.onToggleGCal);
    },
    getState: function getState() {
        var _loopIt = 0;

        var state = {};

        var pluginsEnabled = PS.pluginsAvailable;
        for (var i = 0; i < pluginsEnabled.length; ++i) {
            if (_loopIt++ > 5000 && __infLoop && __infLoop(_loopIt)) throw new RangeError();

            var pluginID = pluginsEnabled[i];

            state[pluginID + 'Enabled'] = PS.getPluginEnabledObservable(pluginID);
        }

        return state;
    },
    onToggleGCal: function onToggleGCal(value) {
        if (value) {
            setTimeout(function () {
                this.openSection(C.SettingsSections.GoogleCalendar);
            }.bind(this), 0);
        }
    },
    openSection: function openSection(name) {
        var ok = !PS.isSaveEnabled.get();

        if (!ok) {
            g.menus.confirm('You have unsaved changes. Are you sure you want to cancel your changes?', function (result) {
                if (result) {
                    PS.isSaveEnabled.set(false);
                    this.openSection(name);
                }
            }.bind(this));
        }

        if (ok) {
            this.sectionOpened.set(name);
        }
    },
    showEarlyAndAccount: function showEarlyAndAccount() {
        return !g.isDemoMode() || DEBUG && DEBUG === 'billing';
    },
    _checkLinkedAccountErrors: function _checkLinkedAccountErrors() {
        var _loopIt2 = 0;

        var hasError = false;

        var syncAccounts = g.vmMain.pluginManager.getPlugin(PluginID.Gmail).getSyncAccounts();

        for (var i = 0; i < syncAccounts.length; ++i) {
            if (_loopIt2++ > 5000 && __infLoop && __infLoop(_loopIt2)) throw new RangeError();

            var accountID = syncAccounts[i];

            if (AccountManager.isConnected()) {
                var accountError = AccountManager.getAccountError(accountID);

                if (accountError) {
                    hasError = true;
                }
            }
        }

        return hasError;
    },
    _checkSectionError: function _checkSectionError(section) {
        var hasError = false;

        switch (section) {
            case C.SettingsSections.Linked:
                hasError = this._checkLinkedAccountErrors();
                break;
        }

        return hasError;
    },
    renderListItem: function renderListItem(text, indent) {
        var hasError = this._checkSectionError(text),
            content = React.createElement(
            'span',
            null,
            text
        );

        if (hasError) {
            content = React.createElement(
                'span',
                null,
                React.createElement(
                    'span',
                    null,
                    content
                ),
                React.createElement('span', { className: 'error icon-warning' })
            );
        }

        return React.createElement(
            FancyListItem,
            { className: indent && !hasError ? 'indented' : null, selected: this.state.sectionOpened === text, onClick: this.openSection.bind(this, text) },
            content
        );
    },
    renderPluginListItem: function renderPluginListItem(pluginID, text) {
        var plugin = g.vmMain.pluginManager.getPlugin(pluginID);

        var hasError = plugin && plugin.hasSettingsError();

        var content = React.createElement(
            'span',
            null,
            text
        );

        if (hasError) {
            content = React.createElement(
                'span',
                null,
                content,
                React.createElement('span', { className: 'error icon-warning' })
            );
        }

        return React.createElement(
            FancyListItem,
            { selected: this.state.sectionOpened === text, onClick: this.openSection.bind(this, text) },
            content
        );
    },
    isPluginEnabled: function isPluginEnabled(pluginID) {
        return this.state[pluginID + 'Enabled'];
    },
    render: function render() {
        var BM = g.billingManager;
        var state = this.state;
        var sectionOpened = state.sectionOpened;
        var content;
        var sectionComponent;
        switch (sectionOpened) {
            case C.SettingsSections.General:
                sectionComponent = ReactModalSettingsGeneral;
                break;
            case C.SettingsSections.Plugins:
                sectionComponent = ReactModalSettingsPlugins;
                break;
            case C.SettingsSections.Invite:
                sectionComponent = ReactInviteSection;
                break;
            case C.SettingsSections.EarlyAccess:
                sectionComponent = ReactModalSettingsEarly;
                break;
            case C.SettingsSections.Account:
                sectionComponent = ReactModalSettingsAccounts;
                break;
            case C.SettingsSections.Linked:
                sectionComponent = ReactModalSettingsLinked;
                break;
            case C.SettingsSections.GoogleCalendar:
                sectionComponent = ReactModalSettingsGCal;
                break;
            case C.SettingsSections.Contacts:
                sectionComponent = ReactModalSettingsContacts;
                break;
            case C.SettingsSections.Gmail:
                sectionComponent = ReactModalSettingsGmail;
                break;
            case C.SettingsSections.Agenda:
                sectionComponent = ReactModalSettingsAgenda;
                break;
            case C.SettingsSections.Dates:
                sectionComponent = ReactModalSettingsDates;
                break;
            case C.SettingsSections.Backups:
                sectionComponent = ReactModalSettingsBackup;
                break;
            default:
                try{g.PAssert(1784,false, 'Unknown settings sections requested: ', sectionOpened);}catch(PERR){g.reportError(PERR);}
                break;
        }

        content = React.createElement(sectionComponent, {
            key: 'content',
            openSection: this.openSection
        });

        var title = 'Settings - ' + this.sectionOpened.get();

        return React.createElement(
            FancyModal,
            { id: 'modalSettings', title: title, width: 800, height: 900, modalID: Modals.Settings },
            React.createElement(
                'div',
                { key: 'sidebar', width: 160 },
                React.createElement(
                    FancyListItem,
                    { header: true },
                    'SETTINGS'
                ),
                this.renderListItem(C.SettingsSections.General),
                this.renderListItem(C.SettingsSections.Dates),
                this.renderListItem(C.SettingsSections.Agenda),
                this.renderListItem(C.SettingsSections.Backups),
                this.showEarlyAndAccount() && this.renderListItem(C.SettingsSections.Account),
                React.createElement(
                    FancyListItem,
                    { header: true },
                    'PLUGINS'
                ),
                this.renderListItem(C.SettingsSections.Plugins),
                this.isPluginEnabled(PluginID.GCalendar) && this.renderPluginListItem(PluginID.GCalendar, C.SettingsSections.GoogleCalendar),
                this.isPluginEnabled(PluginID.Contacts) && this.renderPluginListItem(PluginID.Contacts, C.SettingsSections.Contacts),
                this.isPluginEnabled(PluginID.Gmail) && this.renderPluginListItem(PluginID.Gmail, C.SettingsSections.Gmail),
                this.isPluginEnabled(PluginID.Gmail) && this.showEarlyAndAccount() && this.renderListItem(C.SettingsSections.Linked),
                React.createElement(
                    FancyListItem,
                    { header: true },
                    'SUPPORT'
                ),
                this.renderListItem(C.SettingsSections.EarlyAccess),
                this.renderListItem(C.SettingsSections.Invite)
            ),
            content
        );
    }
});

export default ReactModalSettings;