import g from '~/globals';
import C from 'Constants';
import Autocomplete from 'Autocomplete';
import React from 'react';
import MooReact from 'MooReact';
import DivX from 'Components/DivX';

var NextWordOptions = {
    None: 0x0,
    DayName: 0x1,
    Days: 0x2,
    Time: 0x4,
    Repeat: 0x8
};

var arrSoft = ['now', 'soon', 'later', 'someday'];
var arrSoftDays = ['today', 'tomorrow'];
var arrDayModifiers = ['next'];
var arrDays = ['mon', 'tue', 'wed', 'thu', 'fri', 'sat', 'sun']; // Date.CultureInfo.dayNames
var arrMonths = ['january', 'february', 'march', 'april', 'may', 'june', 'july', 'august', 'september', 'october', 'november', 'december']; // Date.CultureInfo.monthNames
var arrRepeatStart = ['every'];
var arrRepeatModifier = ['first', 'other', 'last'];
var arrDayList = [0, 1, 2, 3, 4, 5, 6];
var arrSoftList = arrSoft.concat(arrRepeatStart);
var arrDict = arrSoftList.concat(arrSoftDays).concat(arrRepeatModifier).concat(arrDayModifiers).concat(arrDays).concat(arrMonths);

var ReactAutocompleteDates = MooReact.createClass({
    displayName: 'ReactAutocompleteDates',
    componentDisplay: C.ComponentDisplay.Everywhere,
    init: function init() {
        this.text = '';

        this.createObservables({
            nextDateWord: NextWordOptions.None
        });
    },
    isInMode: function isInMode(mode) {
        if (mode === 0) return this.state.nextDateWord === 0;else return g.isFlagSet(this.state.nextDateWord, mode);
        // console.log(mode, g.isFlagSet(this.state.nextDateWord, mode), this.state.nextDateWord);
    },
    getShortMonth: function getShortMonth(index) {
        return Date.CultureInfo.abbreviatedMonthNames[index];
    },
    getLongMonth: function getLongMonth(index) {
        return Date.CultureInfo.monthNames[index];
    },
    getShortDay: function getShortDay(index) {
        return Date.CultureInfo.abbreviatedDayNames[index];
    },
    getLongDay: function getLongDay(index) {
        return Date.CultureInfo.dayNames[index];
    },
    getMonthList: function getMonthList() {
        var _loopIt = 0;

        // These two variables are enough to control the number of visible months
        // as well as how far in the past the months start relative to the current one.
        var monthsInPast = 1;
        var monthsInFuture = 5;

        var currentIndex = new Date().getMonth();

        var startIndex = currentIndex - monthsInPast;

        // Help out wrap around to ensure we dont underflow the months array
        if (startIndex < 0) {
            startIndex = 12 + startIndex;
        }

        var endIndex = startIndex + monthsInPast + monthsInFuture;

        var output = [];

        for (var i = startIndex; i < endIndex; ++i) {
            if (_loopIt++ > 5000 && __infLoop && __infLoop(_loopIt)) throw new RangeError();

            var monthIndex = i % 12;

            output.push(monthIndex);
        }

        return output;
    },
    nextWordOptions: function nextWordOptions(text) {
        var words = text.toLowerCase().split(' ');

        var lastWord = words[words.length - 1];

        if (lastWord === '') {
            lastWord = words[words.length - 2];
        }

        if (arrSoft.indexOf(lastWord) >= 0) {
            return NextWordOptions.None;
        }

        if (arrRepeatStart.indexOf(lastWord) >= 0) {
            return NextWordOptions.DayName | NextWordOptions.Repeat;
        }

        if (arrRepeatModifier.indexOf(lastWord) >= 0) {
            return NextWordOptions.DayName;
        }

        if (arrSoftDays.indexOf(lastWord) >= 0) {
            return NextWordOptions.Time;
        }

        if (arrDayModifiers.indexOf(lastWord) >= 0) {
            return NextWordOptions.DayName;
        }

        if (arrDays.indexOf(lastWord) >= 0) {
            return NextWordOptions.Time;
        }

        if (arrMonths.indexOf(lastWord) >= 0) {
            return NextWordOptions.Days;
        }

        var isInt = parseInt(lastWord, 10);

        if (!isNaN(isInt)) {
            return NextWordOptions.Time;
        }

        if (lastWord.indexOf(':') >= 0) {
            return NextWordOptions.Time;
        }

        return NextWordOptions.None;
    },
    onMouseDown: function onMouseDown(e) {
        g.preventAndStop(e);
    },
    onTapDate: function onTapDate(e) {
        var el = e.target;

        if (el.nodeName === 'SPAN') {
            var text = el.getAttribute('data-text') || el.textContent.trim(),
                next = this.nextWordOptions(text),
                hide = next === NextWordOptions.None;

            this.nextDateWord.set(next);

            this.text += text;
            if (!(g.findParentID(el, 'autocompleteHours', 2) || g.findParentID(el, 'autocompleteMinutes', 2))) {
                this.text += ' ';
            }

            this.props.setText(this.text);

            if (hide) {
                this.props.onSelected();
            }
        }
    },
    render: function render() {
        var _this = this,
            nextDateWord = this.state.nextDateWord,
            showAllDates = this.text === '';

        return React.createElement(
            'div',
            { id: 'autocompleteDates', className: 'f1', onMouseDown: this.onMouseDown, onClick: this.onTapDate },
            (this.isInMode(NextWordOptions.None) || this.isInMode(NextWordOptions.DayName)) && React.createElement(
                'div',
                { id: 'autocompleteDate' },
                !this.isInMode(NextWordOptions.Repeat) && React.createElement(
                    'div',
                    { className: g.classNames({ 'disabled': !showAllDates }) },
                    React.createElement(
                        'span',
                        null,
                        'today'
                    ),
                    React.createElement(
                        'span',
                        null,
                        'tomorrow'
                    ),
                    React.createElement(
                        'span',
                        null,
                        'next'
                    ),
                    React.createElement(
                        'span',
                        null,
                        'every'
                    )
                ),
                this.isInMode(NextWordOptions.Repeat) && React.createElement(
                    'div',
                    null,
                    React.createElement(
                        'span',
                        null,
                        'first'
                    ),
                    React.createElement(
                        'span',
                        null,
                        'other'
                    ),
                    React.createElement(
                        'span',
                        null,
                        'last'
                    )
                ),
                React.createElement(
                    'div',
                    { id: 'datePickerDays' },
                    arrDayList.map(function (date) {
                        var l = _this.getLongDay(date);
                        return React.createElement(
                            'span',
                            { key: l },
                            _this.getShortDay(date)
                        );
                    })
                ),
                React.createElement(
                    'div',
                    { className: g.classNames({ 'disabled': !showAllDates }) },
                    _this.getMonthList().map(function (date) {
                        var l = _this.getLongMonth(date);
                        return React.createElement(
                            'span',
                            { key: l, 'data-text': l },
                            _this.getShortMonth(date)
                        );
                    })
                )
            ),
            this.isInMode(NextWordOptions.Days) && React.createElement(
                'div',
                { id: 'autocompleteDay' },
                React.createElement(
                    'div',
                    { className: 'flexcolumn' },
                    React.createElement(
                        'div',
                        { className: 'autocompleteDays' },
                        Array.apply(0, Array(31)).map(function (e, index) {
                            return React.createElement(
                                'span',
                                { key: index },
                                index + 1
                            );
                        })
                    )
                )
            ),
            this.isInMode(NextWordOptions.Time) && React.createElement(
                'div',
                { id: 'autocompleteTime' },
                React.createElement(
                    'div',
                    { className: 'flexcolumn' },
                    React.createElement(
                        'div',
                        { id: 'autocompleteHours' },
                        Array.apply(0, Array(12)).map(function (e, index) {
                            return React.createElement(
                                'span',
                                { key: index },
                                index + 1
                            );
                        })
                    )
                ),
                React.createElement(
                    'div',
                    { className: 'flexcolumn' },
                    React.createElement(
                        'div',
                        { id: 'autocompleteMinutes' },
                        React.createElement(
                            'span',
                            null,
                            ':15'
                        ),
                        React.createElement(
                            'span',
                            null,
                            ':30'
                        ),
                        React.createElement(
                            'span',
                            null,
                            ':45'
                        )
                    )
                ),
                React.createElement(
                    'div',
                    { className: 'flexcolumn' },
                    React.createElement(
                        'div',
                        { id: 'autocompleteAMPM' },
                        React.createElement(
                            'span',
                            null,
                            'am'
                        ),
                        React.createElement(
                            'span',
                            null,
                            'pm'
                        )
                    )
                )
            )
        );
    }
});

export default ReactAutocompleteDates;