import g from '~/globals';
import C from 'Constants';
import React from 'react';
import MooReact from 'MooReact';
import Sel from 'Sel';
import FancyContextMenu from 'Components/FancyContextMenu';
import FancyContextMenuItem from 'Components/FancyContextMenuItem';

var ReactContextMenuOverviewOptions = MooReact.createClass({
    displayName: 'ReactContextMenuOverviewOptions',
    componentDisplay: C.ComponentDisplay.Desktop,
    onClickScroll: function onClickScroll() {
        var props = this.props;
        props.pane.overview.scrollItemIntoView(props.item);
    },
    onClickAddSubproject: function onClickAddSubproject() {
        var props = this.props;

        g.vmMain.zoomin(props.item);

        props.pane.addProject();
    },
    // onClickAddAbove: function ()
    // {

    // },
    // onClickAddBelow: function ()
    // {

    // },
    onClickZoom: function onClickZoom() {
        if (this.props.item.canZoom()) {
            Sel.setFocusedPane(this.props.pane);

            g.vmMain.zoomin(this.props.item);
        }
    },
    render: function render() {
        var pane = this.props.pane;

        return React.createElement(
            FancyContextMenu,
            { key: 'contextMenuGmailSort', id: 'contextMenuGmailSort', element: this.props.element },
            React.createElement(
                FancyContextMenuItem,
                { icon: 'icon-unfold_less', onClick: this.onClickScroll },
                'Scroll to item in pane'
            ),
            React.createElement(
                FancyContextMenuItem,
                { icon: 'icon-add', onClick: this.onClickAddSubproject },
                'Add subproject'
            ),
            React.createElement(
                FancyContextMenuItem,
                { icon: 'icon-zoom-in', onClick: this.onClickZoom },
                'Zoom In'
            )
        );
    }
});

export default ReactContextMenuOverviewOptions;