import g from '~/globals';
import C from 'Constants';
import d from 'data';
import goog from 'goog';
import React from 'react';
import MooReact from 'MooReact';
import PS from 'PluginSettings';
import AccountManager from 'AccountManager';
import FancyCheckbox from 'Components/FancyCheckbox';
import FancyButton from 'Components/FancyButton';

var ReactModalSettingsPlugins = MooReact.createClass({
    displayName: 'ReactModalSettingsPlugins',
    componentDisplay: C.ComponentDisplay.Desktop,
    init: function init() {
        this.pluginsEnabled = {};
        this.pluginOptions = {};

        PS.refresh();
    },
    componentDidMount: function componentDidMount() {
        this.listenTo(AccountManager.onAccountsUpdated);
        this.checkTiers();
    },
    getState: function getState() {
        var _loopIt = 0;

        var state = {
            isSaveEnabled: PS.isSaveEnabled,
            premiumStatus: g.billingManager._premiumStatus,
            linkedAccounts: AccountManager.getLinkedAccounts(),
            hasError: AccountManager.isConnected() && !!AccountManager.getAccountError(AccountManager.getDefaultEmail())
        };

        var pluginsEnabled = PS.pluginsAvailable;

        for (var i = 0; i < pluginsEnabled.length; ++i) {
            if (_loopIt++ > 5000 && __infLoop && __infLoop(_loopIt)) throw new RangeError();

            var pluginID = pluginsEnabled[i];

            this.pluginsEnabled[pluginID] = state[pluginID + 'Enabled'] = PS.getPluginEnabledObservable(pluginID);

            var options = PS.getPluginOptions(pluginID);
            for (var option in options) {
                if (options.hasOwnProperty(option)) {
                    var a = pluginID + '_' + option;
                    this.pluginOptions[a] = state[a] = options[option];
                }
            }
        }

        return state;
    },
    openSection: function openSection(section) {
        this.props.openSection(section);
    },
    onChangeFree: function onChangeFree(value) {
        this.refs[PluginID.Contacts].setValue(value);
        this.refs[PluginID.Gmail].setValue(value);
        this.onChangeFeature();
    },
    onChangePremium: function onChangePremium(value) {
        this.refs[PluginID.GCalendar].setValue(value);
        this.refs[PluginID.Drive].setValue(value);
        this.refs.DriveShared.setValue(value);
        this.onChangeFeature();
    },
    checkTier: function checkTier(ref, arr) {
        var _loopIt2 = 0;

        var value = 0;

        for (var i = 0; i < arr.length; i++) {
            if (_loopIt2++ > 5000 && __infLoop && __infLoop(_loopIt2)) throw new RangeError();

            if (this.refs[arr[i]].getValue()) value++;else value--;
        }

        if (value === arr.length) {
            ref.setValue(true);
        }
        if (value === -arr.length) {
            ref.setValue(false);
        }
    },
    checkTiers: function checkTiers() {
        this.checkTier(this.refs.free, [PluginID.Contacts, PluginID.Gmail]);
        this.checkTier(this.refs.premium, [PluginID.GCalendar, PluginID.Drive, 'DriveShared']);
    },
    onChangeFeature: function onChangeFeature() {
        PS.isSaveEnabled.set(true);
        this.checkTiers();
    },
    onClickFixError: function onClickFixError() {
        PS.enableOfflineFeatures( /*force*/true, function () {
            PS.refresh();
            this.updateState();
        }.bind(this));
    },
    onClickCancel: function onClickCancel(e) {
        for (var key in this.refs) {
            if (this.refs.hasOwnProperty(key)) {
                var checkbox = this.refs[key];
                if (!!checkbox.checked) {
                    checkbox.resetValue();
                }
            }
        }
        this.checkTiers();
    },
    onClickSave: function onClickSave(e, cb) {
        var _this = this;

        var ok = true,
            plugins = [PluginID.Contacts, PluginID.GCalendar, PluginID.Gmail, PluginID.Drive].map(function (id) {
            var enable = !!_this.refs[id].getValue(),
                plugin = g.vmMain.pluginManager.getPlugin(id),
                ret = { id: id, enable: enable };

            if (plugin && !plugin.isAvailable()) {
                ok = false;
            }

            if (id === PluginID.Drive) {
                ret.shared = !!_this.refs.DriveShared.getValue();
            }

            return ret;
        });

        if (!ok) {
            if (g.billingManager.isTrialOver()) {
                this.openSection(C.SettingsSections.Account);

                return false;
            } else {
                g.billingManager.startTrial();
            }
        }

        var didImmediate = false;

        PS.setEnabledPlugins(plugins, /*useOffline*/true, function (success) {
            didImmediate = true;
            cb();
            if (success) {
                PS.isSaveEnabled.set(false);
            }
        });

        return !didImmediate;
    },
    renderPluginOptionCheckbox: function renderPluginOptionCheckbox(pluginID, name, option, displayText) {
        return React.createElement(
            FancyCheckbox,
            { className: 'subcheckbox', ref: name, initialChecked: !!this.state[pluginID + '_' + option], onChange: this.onChangeFeature },
            displayText
        );
    },
    renderPluginCheckbox: function renderPluginCheckbox(pluginID, displayText, icon) {
        return React.createElement(
            FancyCheckbox,
            { ref: pluginID, initialChecked: !!this.state[pluginID + 'Enabled'], onChange: this.onChangeFeature },
            icon,
            displayText
        );
    },
    render: function render() {
        var BM = g.billingManager,
            state = this.state,
            hasPremium = BM.hasPremiumFeatures(),
            isTrialOver = BM.isTrialOver(),
            hasOfflineAuthError = state.hasError;

        return React.createElement(
            'div',
            { key: 'content', className: 'new-modal-group' },
            React.createElement(
                'div',
                { className: 'new-modal-title' },
                React.createElement(
                    'div',
                    { className: 'title' },
                    'Integrate with your other services'
                ),
                React.createElement(
                    'div',
                    { className: 'subtext center c-foreground-light' },
                    'Use ',
                    React.createElement(
                        'b',
                        null,
                        'Moo.do'
                    ),
                    ' to organize everything in one place'
                )
            ),
            React.createElement(
                'div',
                { className: 'setupFeatures featureCheckboxes' },
                hasOfflineAuthError && React.createElement(
                    'div',
                    { className: 'new-modal-row new-modal-row-1c settingError' },
                    React.createElement(
                        'div',
                        { className: 'bold' },
                        'Error detected'
                    ),
                    React.createElement(
                        'div',
                        { className: 'errorSubtext' },
                        'There is an error with your authentication. Click here to fix it.'
                    ),
                    React.createElement(
                        'div',
                        { className: 'center' },
                        React.createElement(
                            FancyButton,
                            { raised: true, onClick: this.onClickFixError },
                            'FIX ERROR'
                        )
                    )
                ),
                React.createElement(
                    'div',
                    { className: 'featureTier c-border' },
                    React.createElement(
                        'div',
                        { className: 'tierTitleWrapper' },
                        React.createElement(
                            FancyCheckbox,
                            { ref: 'free', className: 'tierTitle c-background', initialChecked: false, onChange: this.onChangeFree },
                            'Free Plugins'
                        )
                    ),
                    React.createElement(
                        'div',
                        { className: 'flexbox' },
                        React.createElement(
                            'div',
                            { className: 'setupFeatureHalf' },
                            this.renderPluginCheckbox(PluginID.Contacts, 'Google Contacts', React.createElement('i', { className: 'icon-group' }))
                        ),
                        React.createElement(
                            'div',
                            { className: 'setupFeatureHalf' },
                            this.renderPluginCheckbox(PluginID.Gmail, 'Gmail', React.createElement('img', { src: '/img/plugin-gmail.png' }))
                        )
                    )
                ),
                React.createElement(
                    'div',
                    { className: 'featureTier c-border' },
                    React.createElement(
                        'div',
                        { className: 'tierTitleWrapper' },
                        React.createElement(
                            FancyCheckbox,
                            { ref: 'premium', className: 'tierTitle c-background', initialChecked: false, onChange: this.onChangePremium },
                            'Premium Plugins'
                        )
                    ),
                    !hasPremium && isTrialOver && React.createElement(
                        'div',
                        { className: 'featureTierDesc c-foreground-light' },
                        'Your free trial of Premium has ended. Please signup to continue using premium features or contact support@moo.do to extend.'
                    ),
                    !hasPremium && !isTrialOver && React.createElement(
                        'div',
                        { className: 'featureTierDesc c-foreground-light' },
                        'Enabling Premium features will start a free ' + C.TrialDuration + ' day trial of Moo.do Premium.'
                    ),
                    React.createElement(
                        'div',
                        { className: 'flexbox' },
                        React.createElement(
                            'div',
                            { className: 'setupFeature setupFeatureHalf' },
                            this.renderPluginCheckbox(PluginID.GCalendar, 'Google Calendar', React.createElement('img', { src: '/img/plugin-gcal.png' }))
                        ),
                        React.createElement(
                            'div',
                            { className: 'setupFeature setupFeatureHalf' },
                            this.renderPluginCheckbox(PluginID.Drive, 'Google Drive', React.createElement('img', { src: '/img/plugin-gdrive.png' })),
                            this.renderPluginOptionCheckbox(PluginID.Drive, 'DriveShared', PluginSettingsID.Drive.WriteAccess, 'Share files with collaborators')
                        )
                    )
                ),
                React.createElement(
                    'div',
                    { className: 'setupSubtext' },
                    React.createElement(
                        'b',
                        null,
                        'Moo.do'
                    ),
                    ' connects with your Google services to integrate everything together. ',
                    React.createElement(
                        'b',
                        null,
                        'Moo.do'
                    ),
                    ' data is stored in your personal Google Drive account and is never sent to any other server. Nobody can access any of your data. See our ',
                    React.createElement(
                        'span',
                        { className: 'link' },
                        'Privacy Policy'
                    ),
                    ' for more details.'
                ),
                React.createElement(
                    'div',
                    { className: 'setupButtons center' },
                    React.createElement(
                        FancyButton,
                        { raised: true, primary: true, onClick: this.onClickSave, enabled: state.isSaveEnabled },
                        'SAVE'
                    ),
                    React.createElement(
                        FancyButton,
                        { raised: true, marginLeft: 24, onClick: this.onClickCancel, enabled: state.isSaveEnabled },
                        'RESET'
                    )
                )
            )
        );
    }
});

export default ReactModalSettingsPlugins;