import g from '~/globals';
import C from 'Constants';
import d from 'data';
import gdata from 'gdata';
import platform from 'platform';
import React from 'react';
import MooReact from 'MooReact';
import recovery from 'recovery';
import NativeSel from 'NativeSel';
import FancyModal from 'Components/FancyModal';
import FancyListItem from 'Components/FancyListItem';
import FancyButton from 'Components/FancyButton';

var ReactModalRevisionHistory = MooReact.createClass({
    displayName: 'ReactModalRevisionHistory',
    componentDisplay: C.ComponentDisplay.Desktop,
    // propTypes:
    // {
    // },
    init: function init() {
        this.maxRevision = 0;
        this.revisions = [];
        this.selectedRevision = null;
        this.isOnline = !!gdata.doc;

        this.createObservables({
            isLoading: false,
            recoveredPreview: null
        });

        if (gdata.doc) {
            var _loopIt = 0;

            var revision = recovery.getCurrentRevision() - 1;
            this.maxRevision = revision;
            this.selectedRevision = revision;

            var minRevision = Math.max(revision - window.__MoodoRecovery.maxUIRevisions, 0);

            var arr = [];
            for (var i = revision; i >= minRevision; --i) {
                if (_loopIt++ > 5000 && __infLoop && __infLoop(_loopIt)) throw new RangeError();

                arr.push(i);
            }
            this.revisions = arr;

            this.updatePreview();
        } else {
            this.recoveredPreview.set('You must be online to view your revision history. Please go online and try again.');
        }
    },
    updatePreview: function updatePreview() {
        this.recoveredPreview.set('', /*notify*/false);
        this.isLoading.set(true);
        var docID = recovery.getCurrentDocID();

        recovery.getRevisionData(docID, this.selectedRevision, 'html', function (data) {
            this.recoveredPreview.set(data, /*notify*/false);
            this.isLoading.set(false);
        }.bind(this));
    },
    onSelectChange: function onSelectChange(e) {
        this.selectedRevision = this.revisions[e.target.selectedIndex];
        log(this.selectedRevision);
        this.updatePreview();
    },
    onTapSelect: function onTapSelect() {
        var range = NativeSel.createSelectionRange();

        range.selectNodeContents(this.refs.exportedContent);

        NativeSel.setSelectionRange(range);
    },
    onKeyDown: function onKeyDown(e) {
        var key = platform.getKeyCode(e);

        log(e.metaKey, e.ctrlKey, e.keyCode);

        if (platform.mac && e.metaKey || !platform.mac && e.ctrlKey) {
            if (key === KeyCode.A) {
                this.onTapSelect();
            }
        }

        e.preventDefault();
        e.stopPropagation();
    },
    onTapRestore: function onTapRestore(e, cb) {
        var successCB = function successCB() {
            g.menus.alert({
                text: 'Your data has been recovered.',
                actionText: 'OKAY',
                callback: d.clearDataAndReload
            });
            cb();
        };

        var failureCB = function failureCB() {
            g.menus.alert({
                text: 'There was an error recovering your data. Please contact support@moo.do for additional help.',
                actionText: 'OKAY'
            });
            cb();
        };

        recovery.recoverRevision(this.selectedRevision, successCB, failureCB);

        return true; // FancyInput button should be loading
    },
    render: function render() {
        var classContent = g.classNames({ 'loading': this.state.isLoading });

        return React.createElement(
            FancyModal,
            { id: 'modalRevisionHistory', title: 'Revision History', width: 860, height: 900, modalID: Modals.RevisionHistory },
            React.createElement(
                'div',
                { key: 'sidebar', width: 180 },
                React.createElement(
                    FancyListItem,
                    { header: true },
                    'Choose Revision'
                ),
                React.createElement(
                    FancyListItem,
                    { style: { lineHeight: '24px', height: '36px' }, clickable: false },
                    React.createElement(
                        'select',
                        { onChange: this.onSelectChange },
                        this.revisions.map(function (revision) {
                            return React.createElement(
                                'option',
                                { key: revision, value: revision },
                                revision
                            );
                        })
                    )
                ),
                React.createElement(
                    FancyButton,
                    { primary: true, enabled: this.isOnline, onClick: this.onTapRestore },
                    'RESTORE'
                ),
                React.createElement(
                    FancyListItem,
                    { header: true },
                    'Directions'
                ),
                React.createElement(
                    FancyListItem,
                    { clickable: false, subtext: true },
                    React.createElement(
                        'p',
                        null,
                        'Choose the revision from the dropdown that you want to revert to.'
                    ),
                    React.createElement(
                        'p',
                        null,
                        'Each change is a new revision, so if you\'ve made a lot of changes you may want to go back by 50 or 100.'
                    ),
                    React.createElement(
                        'p',
                        null,
                        'You may need to reload or re-login to other clients to see the recovered data there.'
                    )
                )
            ),
            React.createElement(
                'div',
                { key: 'content', className: classContent },
                React.createElement('div', { className: 'exportedContent', onKeyDown: this.onKeyDown, contentEditable: true, dangerouslySetInnerHTML: { __html: this.state.recoveredPreview }, ref: 'exportedContent' })
            )
        );
    }
});

export default ReactModalRevisionHistory;