import g from '~/globals';
import C from 'Constants';
import React from 'react';
import MooReact from 'MooReact';
import BackupManager from 'BackupManager';
import FancyCheckbox from 'Components/FancyCheckbox';
import FancyInputNumber from 'Components/FancyInputNumber';
import FancySelect from 'Components/FancySelect';
import FancyButton from 'Components/FancyButton';

var ReactModalSettingsBackup = MooReact.createClass({
    displayName: 'ReactModalSettingsBackup',
    componentDisplay: C.ComponentDisplay.Desktop,
    init: function init() {
        this.listenToSetting(Settings.backupFrequency);

        this.createObservables({
            backupText: ''
        });
    },
    getState: function getState() {
        return {
            backupFrequency: g.settings.get(Settings.backupFrequency) || 0
        };
    },
    onToggleEnable: function onToggleEnable() {
        var val = void 0;
        if (this.state.backupFrequency > 0) {
            val = 0;
        } else {
            val = C.DefaultBackupFrequency;
            this.refs.selectTimeframe.setValue('Hours');
        }

        g.settings.set(Settings.backupFrequency, val);
    },
    onChangeFrequency: function onChangeFrequency() {
        var freq = this.refs.frequency.getValue(),
            timeframe = this.refs.selectTimeframe.getValue(),
            mult = timeframe === 'Days' && C.MSPerDay || timeframe === 'Hours' && C.MSPerHour || timeframe === 'Minutes' && C.MSPerMinute;

        g.settings.set(Settings.backupFrequency, freq * mult);
    },
    onClickBackupNow: function onClickBackupNow() {
        var _this = this;

        this.backupText.set('Backing up...');
        BackupManager.backup(function () {
            _this.backupText.set('Backup complete');
        });
    },
    onClickBackupList: function onClickBackupList() {
        g.menus.closeModalNew(Modals.Settings);
        g.menus.openModalNew(Modals.Backups);
    },
    render: function render() {
        var state = this.state,
            freq = this.state.backupFrequency,
            timeframe = 'Hours';

        if (freq > C.MSPerDay) {
            timeframe = 'Days';
            freq = freq / C.MSPerDay;
        } else if (freq > C.MSPerHour) {
            timeframe = 'Hours';
            freq = freq / C.MSPerHour;
        } else if (freq > 0) {
            timeframe = 'Minutes';
            freq = freq / C.MSPerMinute;
        }

        freq = Math.floor(freq);

        return React.createElement(
            'div',
            { key: 'content' },
            React.createElement(
                'div',
                { className: 'modal-row' },
                React.createElement(
                    FancyCheckbox,
                    { checked: freq > 0, onChange: this.onToggleEnable },
                    'Enable automatic backups'
                ),
                React.createElement(
                    'div',
                    { className: 'modal-subtext c-foreground-faded' },
                    'Automatically backup all data into the "Moo.do Backups" folder in your Google Drive. Backups will run on the following frequency, only if there are any new changes to back up.'
                )
            ),
            React.createElement(
                'div',
                { className: 'modal-row' },
                React.createElement(
                    'div',
                    { className: 'settingsName ib bold' },
                    'Every:'
                ),
                React.createElement(FancyInputNumber, { value: freq, ref: 'frequency', minValue: 0, maxValue: 60, interval: 1, onChange: this.onChangeFrequency }),
                React.createElement(
                    FancySelect,
                    { defaultValue: timeframe, style: { marginLeft: 16, height: 32 }, ref: 'selectTimeframe', onChange: this.onChangeFrequency },
                    React.createElement(
                        'option',
                        { value: 'Minutes' },
                        'Minutes'
                    ),
                    React.createElement(
                        'option',
                        { value: 'Hours' },
                        'Hours'
                    ),
                    React.createElement(
                        'option',
                        { value: 'Days' },
                        'Days'
                    )
                )
            ),
            React.createElement(
                'div',
                { className: 'modal-row' },
                React.createElement(
                    FancyButton,
                    { raised: true, onClick: this.onClickBackupNow },
                    'BACKUP NOW'
                ),
                state.backupText && React.createElement(
                    'div',
                    { className: 'modal-subtext' },
                    state.backupText
                )
            ),
            React.createElement(
                'div',
                { className: 'modal-row' },
                React.createElement(
                    FancyButton,
                    { raised: true, onClick: this.onClickBackupList },
                    'SEE BACKUP HISTORY'
                )
            )
        );
    }
});

export default ReactModalSettingsBackup;