var BillingFailureCode = {
    None: null,
    NoBraintreeGlobal: 1,
    AddScriptFailure: 2,
    NoRemoteToken: 3,
    RemoteTokenParse: 4,
    ContactingServer: 5,
    NoClientIdentifier: 6
};

var SignupFailureCode = {
    None: null,
    MissingNonce: 1,
    MissingPlanID: 2,
    ServerFailure: 3,
    UnauthorizedUser: 4,
    InvalidParameter: 5,
    MissingParameter: 6,
    BraintreeUnknown: 7,
    BraintreePaypalBlock: 8,
    BraintreeInvalidCCN: 9
};

var premiumPlans = ['2m4b', 'gjgm', 'pmjw', '5pm2'];

if (DEBUG) {
    premiumPlans = ['58qg', 'pnr2', '6vr2', 'mt4b'];
}

import React from 'react';
import C from 'Constants';
import g from '~/globals';
import MooReact from 'MooReact';
import FancyButton from 'Components/FancyButton';
import FancyLoader from 'Components/FancyLoader';
import FancyModal from 'Components/FancyModal';

var ReactModalBilling = MooReact.createClass({
    displayName: 'ReactModalBilling',
    componentDisplay: C.ComponentDisplay.Desktop,
    init: function init() {
        // Dependencies
        this._braintreeRequested = false;
        this._braintreeLoaded = false;

        this._clientTokenRequested = false;
        this._clientTokenLoaded = false;

        this._clientToken = undefined;
        this._btClient = undefined;
        this._btIntegration = undefined;

        this._isMethodUpdate = this.props.isUpdate;

        this.createObservables({
            isSubmitting: false,
            isLoading: true,
            billingFailureCode: BillingFailureCode.None,
            signupFailureCode: SignupFailureCode.None,
            signupErrorMessage: undefined
        });

        this._ensureBraintreeLib();

        this._ensureClientToken();
    },
    onUnmount: function onUnmount() {
        if (this._btIntegration) {
            this._btIntegration.teardown();

            this._btIntegration = undefined;
        }
    },
    _handlePaymentMethodUpdate: function _handlePaymentMethodUpdate(obj) {
        if (this._isMethodUpdate) {
            if (obj && obj.nonce) {
                this._processUpdateNonce( /*err*/undefined, obj.nonce);
            } else {
                this._processUpdateNonce({ error: true }, /*nonce*/undefined);
            }
        } else {
            if (obj && obj.nonce) {
                this._processPaymentNonce( /*err*/undefined, obj.nonce);
            } else {
                this._processPaymentNonce({ error: true }, /*nonce*/undefined);
            }
        }
    },
    _notifyDependencyLoaded: function _notifyDependencyLoaded() {
        if (this._braintreeLoaded && this._clientTokenLoaded) {
            this._btClient = new braintree.api.Client({ clientToken: this._clientToken });

            braintree.setup(this._clientToken, 'dropin', {
                container: 'braintreeDropin',
                onPaymentMethodReceived: this._handlePaymentMethodUpdate,
                onError: function onError(err) {
                    g.reportError(err);
                },
                onReady: function (btIntegration) {
                    this.isLoading.set(false);

                    this._btIntegration = btIntegration;
                }.bind(this)
            });
        }
    },
    _notifyDependencyLoadError: function _notifyDependencyLoadError(code) {
        this.billingFailureCode.set(code);

        g.reportError(new Error('Client hit error in billing: ' + code));

        g.billingManager.setLastSignupError(code);

        g.sendEvent('Billing', 'SignupError_' + code);
    },
    _ensureBraintreeLib: function _ensureBraintreeLib() {
        if (!this._braintreeRequested) {
            if (!window.braintree) {
                this._braintreeRequested = true;

                g.addScriptHack('https://js.braintreegateway.com/v2/braintree.js', 'braintreeScript', function () {
                    if (window.braintree) {
                        this._braintreeLoaded = true;

                        this._notifyDependencyLoaded();
                    } else {
                        this._braintreeRequested = false;

                        this._notifyDependencyLoadError(BillingFailureCode.NoBraintreeGlobal);
                    }
                }.bind(this), function () {
                    this._braintreeRequested = false;

                    this._notifyDependencyLoadError(BillingFailureCode.AddScriptFailure);
                }.bind(this));
            } else {
                this._braintreeLoaded = true;

                this._notifyDependencyLoaded();
            }
        }
    },
    _ensureClientToken: function _ensureClientToken() {
        var clientId = g.billingManager.getBillingIdentifier();

        if (clientId) {
            if (!this._clientTokenRequested) {
                this._clientTokenRequested = true;

                g.XHR_PrivateAPI({
                    type: 'POST',
                    path: '/billing/getToken',
                    data: { email: clientId },
                    requireAuth: true,
                    autoRetry: true,
                    maxRetries: 1,
                    cb: function (xhr) {
                        if (xhr.status === 200) {
                            this._clientTokenLoaded = true;

                            try {
                                var response = JSON.parse(xhr.responseText).data;

                                if (response && response.token) {
                                    this._clientToken = response.token;

                                    this._notifyDependencyLoaded();
                                } else {
                                    this._notifyDependencyLoadError(BillingFailureCode.NoRemoteToken);
                                }
                            } catch (err) {
                                g.reportError(err);

                                this._notifyDependencyLoadError(BillingFailureCode.RemoteTokenParse);
                            }
                        } else {
                            this._clientTokenRequested = false;

                            this._notifyDependencyLoadError(BillingFailureCode.ContactingServer);
                        }
                    }.bind(this)
                });
            }
        } else {
            this._notifyDependencyLoadError(BillingFailureCode.NoClientIdentifier);
        }
    },
    _setPaymentResponse: function _setPaymentResponse(statusData) {
        switch (statusData.code) {
            case PrivateAPIErrorCodes.UnauthorizedUser:
                this.signupFailureCode.set(SignupFailureCode.UnauthorizedUser);
                this.signupErrorMessage.set(undefined);
                break;
            case PrivateAPIErrorCodes.InvalidParameter:
                this.signupFailureCode.set(SignupFailureCode.InvalidParameter);
                this.signupErrorMessage.set(undefined);
                break;
            case PrivateAPIErrorCodes.MissingParameter:
                this.signupFailureCode.set(SignupFailureCode.MissingParameter);
                this.signupErrorMessage.set(undefined);
                break;
            case PrivateAPIErrorCodes.BraintreePaypalBlock:
                this.signupFailureCode.set(SignupFailureCode.BraintreePaypalBlock);
                this.signupErrorMessage.set(statusData.data);
                break;
            case PrivateAPIErrorCodes.BraintreeInvalidCCN:
                this.signupFailureCode.set(SignupFailureCode.BraintreeInvalidCCN);
                this.signupErrorMessage.set(statusData.data);
                break;
            case PrivateAPIErrorCodes.BraintreeUnknown:
                this.signupFailureCode.set(SignupFailureCode.BraintreeUnknown);
                this.signupErrorMessage.set(statusData.data);
                break;
            default:
                this.signupFailureCode.set(SignupFailureCode.ServerFailure);
                this.signupErrorMessage.set(undefined);
                break;
        }
    },
    _processUpdateNonce: function _processUpdateNonce(err, nonce) {
        g.sendEvent('Billing', 'PremiumUpdate');

        if (!err && nonce) {
            if (!this.isSubmitting.get()) {
                this.isSubmitting.set(true);

                g.XHR_PrivateAPI({
                    type: 'POST',
                    path: '/billing/updateMethod',
                    data: { nonce: nonce },
                    requireAuth: true,
                    cb: function (xhr) {
                        this.isSubmitting.set(false);

                        if (xhr.status === 200) {
                            if (g.menus.isModalOpen(this.id)) {
                                this.onClickClose();
                            }

                            g.menus.alert({
                                text: 'Your payment information has been updated.',
                                actionText: 'OKAY'
                            });

                            g.sendEvent('Billing', 'PremiumUpdateSUCCESS');
                        } else {
                            if (xhr.response) {
                                try {
                                    var statusData = JSON.parse(xhr.response);

                                    this._setPaymentResponse(statusData);
                                } catch (parseErr) {
                                    g.reportError(parseErr);

                                    this.signupFailureCode.set(SignupFailureCode.ServerFailure);
                                    this.signupErrorMessage.set(undefined);
                                }
                            } else {
                                this.signupFailureCode.set(SignupFailureCode.ServerFailure);
                                this.signupErrorMessage.set(undefined);
                            }

                            g.sendEvent('Billing', 'PremiumUpdateFAILURE');
                        }
                    }.bind(this)
                });
            }
        }
    },
    _processPaymentNonce: function _processPaymentNonce(err, nonce) {
        g.sendEvent('Billing', 'PremiumSignup');

        if (!err && nonce) {
            this.signupFailureCode.set(SignupFailureCode.None);
            this.signupErrorMessage.set(undefined);

            var planID = premiumPlans[g.billingManager.getBillingPeriod()];
            try{g.PAssert(1790,planID, 'Must have selected a valid plan to signup');}catch(PERR){g.reportError(PERR);}

            if (planID) {
                if (!this.isSubmitting.get()) {
                    this.isSubmitting.set(true);

                    g.XHR_PrivateAPI({
                        type: 'POST',
                        path: '/billing/signup',
                        data: { email: g.billingManager.getBillingIdentifier(), nonce: nonce, planID: planID },
                        requireAuth: true,
                        cb: function (xhr) {
                            this.isSubmitting.set(false);

                            if (xhr.status === 200) {
                                g.billingManager.setPremiumStatus(PremiumStatus.Active);

                                if (g.menus.isModalOpen(this.id)) {
                                    this.onClickClose();
                                }

                                g.menus.alert({
                                    text: 'Thank you for signing up for <b>Moo.do</b> premium!',
                                    actionText: 'OKAY'
                                });

                                g.sendEvent('Billing', 'PremiumSignupSUCCESS');

                                var value = g.parseInt(g.billingManager.getPlanPrice(premiumPlans.indexOf(planID)));

                                g.reportAdConversion(C.ConversionID.Purchase, value);
                            } else {
                                if (xhr.response) {
                                    try {
                                        var statusData = JSON.parse(xhr.response);

                                        this._setPaymentResponse(statusData);
                                    } catch (parseErr) {
                                        g.reportError(parseErr);

                                        this.signupFailureCode.set(SignupFailureCode.ServerFailure);
                                        this.signupErrorMessage.set(undefined);
                                    }
                                } else {
                                    this.signupFailureCode.set(SignupFailureCode.ServerFailure);
                                    this.signupErrorMessage.set(undefined);
                                }

                                g.sendEvent('Billing', 'PremiumSignupFAILURE');
                            }
                        }.bind(this)
                    });
                }
            } else {
                this.signupFailureCode.set(SignupFailureCode.MissingPlanID);
                this.signupErrorMessage.set(undefined);

                g.sendEvent('Billing', 'PremiumSignupFAILURE');
            }
        } else {
            this.signupFailureCode.set(SignupFailureCode.MissingNonce);
            this.signupErrorMessage.set(undefined);

            g.sendEvent('Billing', 'PremiumSignupFAILURE');
        }
    },
    onClickSubmit: function onClickSubmit() {
        this.refs.form.submit();
    },
    onClickClose: function onClickClose() {
        this.refs.modal.close();
    },
    render: function render() {
        var state = this.state,
            height = !state.billingFailureCode && !state.signupFailureCode ? 400 : 200,
            clsButton = g.classNames('billingSubmit loading20 right', { 'loading': state.isSubmitting }, { 'disabled': state.isLoading }),
            signupErrorMessage = state.signupErrorMessage ? state.signupErrorMessage : 'There was an error processing your payment. We will follow up with you shortly.';

        return React.createElement(
            FancyModal,
            { id: 'modalBilling', title: 'Enter Billing Info', width: 400, height: height, backdropClose: false, scrollable: false, modalID: Modals.Billing, ref: 'modal' },
            React.createElement(
                'div',
                { key: 'content' },
                !state.billingFailureCode && !state.signupFailureCode && React.createElement(
                    'form',
                    { id: 'billingForm', method: 'post', action: 'javascript:void(0);', ref: 'form' },
                    React.createElement('div', { id: 'braintreeDropin' }),
                    state.isLoading && React.createElement(
                        FancyLoader,
                        { center: true },
                        'Loading billing system...'
                    ),
                    !state.billingFailureCode && React.createElement(
                        'div',
                        { className: clsButton },
                        React.createElement('input', { type: 'submit', className: 'new-modal-button mat-button-raised mat-button-blue', value: 'SUBMIT' })
                    )
                ),
                state.signupFailureCode && React.createElement(
                    'div',
                    { id: 'billingSignupError' },
                    signupErrorMessage
                ),
                state.billingFailureCode && React.createElement(
                    'div',
                    { id: 'billingError' },
                    'There was an error loading our payment system. Please contact support@moo.do with code ' + state.billingFailureCode + ' or try again in a few minutes.'
                )
            ),
            React.createElement(
                'div',
                { key: 'buttons', style: { textAlign: 'right' } },
                state.billingFailureCode && React.createElement(
                    FancyButton,
                    { primary: true, onClick: this.onClickClose },
                    'OK'
                )
            )
        );
    }
});

export default ReactModalBilling;