import g from '~/globals';
import C from 'Constants';
import React from 'react';
import MooReact from 'MooReact';
import NativeSel from 'NativeSel';
import FancyButton from 'Components/FancyButton';
import FancyInput from 'Components/FancyInput';

var ReactInviteSection = MooReact.createClass({
    displayName: 'ReactInviteSection',
    componentDisplay: C.ComponentDisplay.Desktop,
    // propTypes:
    // {
    // },
    getState: function getState() {
        var BM = g.billingManager;

        return {
            validBilling: BM._hasValidBillingData,
            premiumStatus: BM._premiumStatus,
            inviteEmailSuccess: BM.inviteEmailSuccess,
            inviteEmailError: BM.inviteEmailError,
            inviteCodeError: BM.inviteCodeError
        };
    },
    onTapInviteUrl: function onTapInviteUrl(e) {
        var el = e.target;
        if (el !== document.activeElement) {
            NativeSel.selectElement(el);
            e.preventDefault();

            g.sendEvent('Billing', 'TapInviteUrl');
        }
    },
    inviteEmails: function inviteEmails() {
        var _loopIt = 0;

        var tokens = this.refs.inviteEmails.getTokens();
        var emails = [];

        for (var i = 0; i < tokens.length; i++) {
            if (_loopIt++ > 5000 && __infLoop && __infLoop(_loopIt)) throw new RangeError();

            emails.push(tokens[i].email);
        }
        g.billingManager.sendInviteEmail(emails);
    },
    render: function render() {
        var BM = g.billingManager;

        var state = this.state;

        return React.createElement(
            'span',
            { className: 'new-modal-group' },
            React.createElement(
                'div',
                { className: 'new-modal-title' },
                React.createElement(
                    'div',
                    { className: 'title' },
                    'Share Moo.do with your friends'
                ),
                React.createElement(
                    'div',
                    { className: 'subtext center' },
                    'Thank you for spreading the ',
                    React.createElement('i', { className: 'icon-heart red' })
                )
            ),
            React.createElement(
                'div',
                { className: 'new-modal-center' },
                React.createElement(
                    'div',
                    null,
                    React.createElement(
                        'div',
                        { className: 'subtitle' },
                        'Social'
                    ),
                    React.createElement(
                        'span',
                        { className: 'shareIcons' },
                        React.createElement(FancyButton, { iconSize: 22, icon: 'icon-twitter', tooltip: '@MooDoApp', onClick: BM.shareTwitter.bind(BM) }),
                        React.createElement(FancyButton, { iconSize: 22, marginLeft: 16, marginRight: 16, icon: 'icon-facebook', tooltip: '@MooDoApp', onClick: BM.shareFacebook.bind(BM) }),
                        React.createElement(FancyButton, { iconSize: 22, icon: 'icon-google-plus', tooltip: '@MooDoApp', onClick: BM.shareGoogle.bind(BM) })
                    ),
                    React.createElement('br', null),
                    React.createElement('br', null),
                    React.createElement(
                        'div',
                        { className: 'subtitle' },
                        'Share Link'
                    ),
                    React.createElement(
                        'div',
                        { className: 'subtext' },
                        BM.getSelectText()
                    ),
                    React.createElement(FancyInput, { display: 'inline-block', width: 280, height: 36, value: BM.getInviteUrl(), onMouseDown: this.onTapInviteUrl, readonly: true }),
                    React.createElement('br', null),
                    React.createElement('br', null),
                    React.createElement(
                        'div',
                        { className: 'subtitle' },
                        'Share by email'
                    ),
                    React.createElement(
                        'div',
                        { className: 'subtext' },
                        'Separate multiple email addresses with commas.'
                    ),
                    React.createElement(FancyInput, { ref: 'inviteEmails', width: 350, buttonWidth: 70, height: 36, autocompleteMode: 'email', placeholder: 'Add friends\' email addresses', buttonText: 'SEND', onButtonClick: this.inviteEmails }),
                    (state.inviteEmailSuccess || state.inviteEmailError) && React.createElement(
                        'div',
                        { id: 'inviteEmailMessage', className: g.classNames({ 'success': state.inviteEmailSuccess }, { 'error': state.inviteEmailError }) },
                        state.inviteEmailSuccess || state.inviteEmailError
                    )
                )
            )
        );
    }
});

export default ReactInviteSection;