import g from '~/globals';
import C from 'Constants';
import d from 'data';
import platform from 'platform';
import React from 'react';
import MooReact from 'MooReact';
import NativeSel from 'NativeSel';
import FancyButton from 'Components/FancyButton';
import FancyListItem from 'Components/FancyListItem';
import FancyCheckbox from 'Components/FancyCheckbox';
import FancyModal from 'Components/FancyModal';

var ReactModalExport = MooReact.createClass({
    displayName: 'ReactModalExport',
    componentDisplay: C.ComponentDisplay.Desktop,
    // propTypes:
    // {
    // },
    init: function init() {
        this.createObservables({
            exportArchived: false,
            includeNotes: true,
            includePrefixes: true,
            dataType: 'html'
        });
    },
    setDataType: function setDataType(type) {
        this.dataType.set(type);
    },
    exportData: function exportData() {
        var root = d.getRootItem(),
            _state = this.state,
            exportArchived = _state.exportArchived,
            includeNotes = _state.includeNotes,
            includePrefixes = _state.includePrefixes,
            content,
            preview;


        if (this.dataType.get() === 'html') {
            content = d.toHtml(root, exportArchived, includeNotes, includePrefixes);
            preview = content;
        } else if (this.dataType.get() === 'text') {
            var tab = '&nbsp;&nbsp;&nbsp;&nbsp;';

            content = d.toPlainText(root, /*indent*/0, exportArchived, includeNotes, includePrefixes);
            preview = content.replace(/\n/g, '<br>').replace(/\t/g, tab);
        } else if (this.dataType.get() === 'json') {
            content = d.toJSON(root, /*hasPrev*/false, exportArchived, includeNotes, includePrefixes);
            preview = content;
        }

        this.exportedContent = content;
        return preview;
    },
    onChangeArchiveItems: function onChangeArchiveItems(value) {
        if (value) {
            // Ensure that archived items have been parsed
            g.vmMain.parseSubtreeArchive(g.vmMain.root(), ChangeType.Auto);
        }

        this.exportArchived.set(value);
    },
    onChangeIncludePrefixes: function onChangeIncludePrefixes(value) {
        this.includePrefixes.set(value);
    },
    onChangeIncludeNotes: function onChangeIncludeNotes(value) {
        this.includeNotes.set(value);
    },
    onTapOpen: function onTapOpen() {
        var content = this.dataType.get() === 'text' ? this.exportData() : this.exportedContent;
        var newWin = open('url');

        newWin.document.write(content);
    },
    onTapSelect: function onTapSelect() {
        var range = NativeSel.createSelectionRange();

        range.selectNodeContents(this.refs.exportedContent);

        NativeSel.setSelectionRange(range);
    },
    onTapSaveExport: function onTapSaveExport() {
        var ext = '';
        if (this.dataType.get() === 'html') {
            ext = 'html';
        } else if (this.dataType.get() === 'text') {
            ext = 'txt';
        } else if (this.dataType.get() === 'json') {
            ext = 'json';
        }

        try {
            g.saveFile(this.exportedContent, 'moodo.' + ext);
        } catch (err) {
            g.reportError(err);
        }
    },
    onKeyDown: function onKeyDown(e) {
        var key = platform.getKeyCode(e);

        if (platform.mac && e.metaKey || !platform.mac && e.ctrlKey) {
            if (key === KeyCode.A) {
                this.onTapSelect();
            }
        }

        e.preventDefault();
        e.stopPropagation();
    },
    render: function render() {
        var dataType = this.state.dataType,
            classExported = g.classNames('exportedContent', dataType),
            exportedPreview = this.exportData();

        // TODOMODALS FancyListItem buttons need to act like buttons

        return React.createElement(
            FancyModal,
            { id: 'modalExport', title: 'Export Data', width: 860, height: 900, modalID: Modals.ExportData },
            React.createElement(
                'div',
                { key: 'sidebar', width: 180 },
                React.createElement(
                    FancyListItem,
                    { header: true },
                    'Data Type'
                ),
                React.createElement(
                    FancyListItem,
                    { selected: dataType === 'html', onClick: this.setDataType.bind(this, 'html') },
                    'HTML'
                ),
                React.createElement(
                    FancyListItem,
                    { selected: dataType === 'text', onClick: this.setDataType.bind(this, 'text') },
                    'Plain Text'
                ),
                React.createElement(
                    FancyListItem,
                    { selected: dataType === 'json', onClick: this.setDataType.bind(this, 'json') },
                    'Moo.do Backup'
                ),
                React.createElement(
                    FancyListItem,
                    { header: true },
                    'Options'
                ),
                React.createElement(
                    FancyListItem,
                    null,
                    React.createElement(
                        FancyCheckbox,
                        { checked: this.state.includePrefixes, onChange: this.onChangeIncludePrefixes },
                        'Include Prefixes'
                    )
                ),
                React.createElement(
                    FancyListItem,
                    null,
                    React.createElement(
                        FancyCheckbox,
                        { checked: this.state.includeNotes, onChange: this.onChangeIncludeNotes },
                        'Include Notes'
                    )
                ),
                React.createElement(
                    FancyListItem,
                    null,
                    React.createElement(
                        FancyCheckbox,
                        { checked: this.state.exportArchived, onChange: this.onChangeArchiveItems },
                        'Archived Items'
                    )
                ),
                React.createElement(
                    FancyListItem,
                    { header: true },
                    'Export'
                ),
                !platform.app && React.createElement(
                    FancyButton,
                    { onClick: this.onTapOpen },
                    'OPEN IN TAB'
                ),
                React.createElement(
                    FancyButton,
                    { onClick: this.onTapSelect },
                    'SELECT'
                ),
                React.createElement(
                    FancyButton,
                    { onClick: this.onTapSaveExport },
                    'SAVE'
                )
            ),
            React.createElement(
                'div',
                { key: 'content' },
                React.createElement('div', { className: classExported, onKeyDown: this.onKeyDown, contentEditable: true, dangerouslySetInnerHTML: { __html: exportedPreview }, ref: 'exportedContent' })
            )
        );
    }
});

export default ReactModalExport;