import g from '~/globals';
import C from 'Constants';
import gdata from 'gdata';
import goog from 'goog';
import React from 'react';
import MooReact from 'MooReact';
import AccountManager from 'AccountManager';
import PluginSettings from 'PluginSettings';
import FancyLoader from 'Components/FancyLoader';
import FancyButton from 'Components/FancyButton';
import FancyCheckbox from 'Components/FancyCheckbox';
import FancyInput from 'Components/FancyInput';
import FancyTable from 'Components/FancyTable';

var ReactModalSettingsLinked = MooReact.createClass({
    displayName: 'ReactModalSettingsLinked',
    componentDisplay: C.ComponentDisplay.Desktop,
    // propTypes:
    // {
    // },
    init: function init() {
        this.createObservables({
            addingAccount: false,
            errorMessage: '',
            linkingAccount: false
        });

        PluginSettings.refresh();
    },
    componentDidMount: function componentDidMount() {
        this.listenTo(AccountManager.onAccountsUpdated);
    },
    getState: function getState() {
        var state = {
            gmailSyncAccounts: PluginSettings.GmailSyncAccounts,
            linkedAccounts: AccountManager.getLinkedAccounts()
        };

        return state;
    },
    linkAccount: function linkAccount(email, addScopes, cb) {
        var scopes = [GScope.UserInfoEmail];

        scopes.pushArray(addScopes);

        return goog.linkAccount(email, scopes, cb);
    },
    removeAccount: function removeAccount(email) {
        this.errorMessage.set('');

        var pluginGmail = g.vmMain.pluginManager.getPlugin(PluginID.Gmail);

        AccountManager.removeLinkedAccount(email, function (success) {
            if (!success) {
                g.toasts.pushMessage({
                    text: 'There was an error unlinking your account.',
                    actionText: 'RETRY',
                    action: this.removeAccount.bind(this, email)
                });
            }
        }.bind(this));

        if (pluginGmail.hasSyncAccount(email)) {
            pluginGmail.removeSyncAccount(email, function () {
                PluginSettings.refresh();
            });
        }
    },
    addAccount: function addAccount(email) {
        if (AccountManager.isDefaultEmail(email)) {
            g.toasts.pushMessage({
                text: 'Please use the \'Plugins\' section to manage your main account.'
            });
        } else {
            AccountManager.addLinkedAccount(email);

            {
                this.enableAccountSync(email, /*value*/true);
            }
        }
    },
    enableAccountSync: function enableAccountSync(email, value) {
        var _this = this;

        this.errorMessage.set('');

        var pluginGmail = g.vmMain.pluginManager.getPlugin(PluginID.Gmail),
            pluginCalendar = g.vmMain.pluginManager.getPlugin(PluginID.GCalendar);

        var reportFailure = function reportFailure() {
            _this.errorMessage.set('There was an error linking your account. Please try again in a minute.');

            PluginSettings.refresh();
        };

        if (value) {
            var scopes = [];

            if (pluginGmail.isEnabled()) {
                scopes.pushArray(pluginGmail.googleScopes());
            }
            if (pluginCalendar.isEnabled()) {
                scopes.pushArray(pluginCalendar.googleScopes());
            }

            if (scopes.length > 0) {
                var linkingAccount = this.linkingAccount;
                linkingAccount.set(email);

                this.linkAccount(email, scopes, function (linkSuccess, linkedEmail) {
                    if (linkSuccess) {
                        pluginGmail.addSyncAccount(linkedEmail, function (success) {
                            if (!success) {
                                reportFailure();
                            }

                            linkingAccount.set(false);
                            PluginSettings.refresh();
                        });
                    } else {
                        linkingAccount.set(false);
                        reportFailure();
                    }
                });
            }
        } else {
            pluginGmail.removeSyncAccount(email, function (success) {
                if (!success) {
                    reportFailure();
                }

                PluginSettings.refresh();
            });
        }
    },
    onAddAccountClick: function onAddAccountClick() {
        this.errorMessage.set('');

        this.addingAccount.set(true);

        setTimeout(function () {
            this.refs.addAccount.focus();
        }.bind(this), 0);
    },
    onAddButtonClick: function onAddButtonClick() {
        this.errorMessage.set('');

        var email = this.refs.addAccount.getValue();

        this.addAccount(email);

        this.addingAccount.set(false);
    },
    handleGmailClick: function handleGmailClick() {
        if (!g.vmMain.pluginManager.getPlugin(PluginID.Gmail).isEnabled()) {
            this.props.openSection(C.SettingsSections.Plugins);
        }
    },
    getAccountList: function getAccountList() {
        var accounts = [],
            defaultAccount = AccountManager.getDefaultEmail();

        if (defaultAccount !== 'me') {
            accounts.push(defaultAccount);
        }

        accounts.pushOnlyUniques(this.state.linkedAccounts);
        accounts.pushOnlyUniques(this.state.gmailSyncAccounts);

        return accounts;
    },
    renderAccount: function renderAccount(email) {
        var isGmailPluginEnabled = g.vmMain.pluginManager.getPlugin(PluginID.Gmail).isEnabled(),
            isGmailEnabled = this.state.gmailSyncAccounts.indexOf(email) >= 0,
            isDefault = AccountManager.isDefaultEmail(email),
            hasError = isGmailEnabled && AccountManager.isConnected() && (!AccountManager.isAuthorized(email, GScope.GmailCompose) || !AccountManager.isCurrentlyAuthorized(email)),
            isLinkingAccount = this.state.linkingAccount === email,
            clsRow = g.classNames('accountRow c-border', { 'linking': isLinkingAccount });

        return React.createElement(
            'div',
            { key: 'row' + email, className: clsRow },
            isDefault ? React.createElement('div', { style: { width: 50 } }) : React.createElement(FancyButton, { icon: 'icon-close', iconSize: 20, width: 50, height: 50, onClick: this.removeAccount.bind(this, email), tooltip: 'Remove Account' }),
            React.createElement(
                'div',
                null,
                !isLinkingAccount ? email : ''
            ),
            isDefault ? React.createElement(FancyCheckbox, { checked: true, disabled: true }) : React.createElement(FancyCheckbox, { disabled: !isGmailPluginEnabled, checked: isGmailEnabled, error: hasError, onClick: this.handleGmailClick, onChange: this.enableAccountSync.bind(this, email) }),
            isLinkingAccount && React.createElement(
                FancyLoader,
                { inline: true },
                'Adding account...'
            )
        );
    },
    render: function render() {
        var accountList = this.getAccountList();

        var accountTable = React.createElement(
            FancyTable,
            { className: 'gmailAccountTable' },
            React.createElement(
                'div',
                { key: 'header' },
                React.createElement('div', null),
                React.createElement(
                    'div',
                    null,
                    'Account'
                ),
                React.createElement(
                    'div',
                    null,
                    'Enabled'
                )
            ),
            accountList.map(this.renderAccount)
        );

        return React.createElement(
            'div',
            { key: 'content', className: 'new-modal-group' },
            React.createElement(
                'div',
                { className: 'new-modal-title' },
                React.createElement(
                    'div',
                    { className: 'title' },
                    'Email Accounts'
                ),
                React.createElement(
                    'div',
                    { className: 'subtext center' },
                    'Manage email from multiple Gmail accounts'
                )
            ),
            accountTable,
            this.state.addingAccount && React.createElement(
                'div',
                { key: 'footer', style: { padding: '0 32px' } },
                React.createElement(FancyInput, { ref: 'addAccount', floatingText: 'Account Email', padding: { left: 24, right: 24 }, height: 50, buttonText: 'ADD', onButtonClick: this.onAddButtonClick })
            ),
            this.state.errorMessage && React.createElement(
                'div',
                { key: 'errorMessage', style: { textAlign: 'center' } },
                this.state.errorMessage
            ),
            !this.state.addingAccount && React.createElement(
                'div',
                { key: 'footer' },
                React.createElement(
                    'div',
                    { style: { textAlign: 'center' } },
                    React.createElement(
                        FancyButton,
                        { height: 50, onClick: this.onAddAccountClick },
                        'ADD ACCOUNT'
                    )
                )
            )
        );
    }
});

export default ReactModalSettingsLinked;