import g from '~/globals';
import d from 'data';
import gdata from 'gdata';
import AccountManager from 'AccountManager';

function ref(o) {
    return o ? o.value : undefined;
}

var recoverPluginRegexp = /(?:(p:[^]+))/ig; // WARNING: This line contains invisible characters after the 's, careful when editing
function val(o) {
    var value;

    if (o) {
        var match;

        recoverPluginRegexp.lastIndex = 0;
        if ((match = recoverPluginRegexp.exec(o.json)) !== null) {
            value = o.json.substr(0, match.index) + window.EmbedAttachChar + match[1] + window.EmbedAttachChar + o.json.substr(match.index + match[0].length);
        } else {
            value = o.json;
        }
    }

    return value;
}

function Recovery() {}

Recovery.prototype = {
    _fixPlugins: function _fixPlugins(rawData) {
        this.transformRawRevisionData(rawData, function (node) {
            if (node.t) {
                var newValue = val(node.t);

                if (newValue !== node.t.json) {
                    log('Fixed Plugin: ' + newValue);
                }

                node.t.json = newValue;
            }
        });
    },
    recoverRevision: function recoverRevision(rev, successCB, failureCB) {
        var docID = this.getCurrentDocID();

        this.getRawRevisionData(docID, rev, function (rawData) {
            if (!rawData) {
                log('Unable to get revision data');

                return failureCB();
            }

            this._fixPlugins(rawData);

            this.runRecovery(docID, rawData, successCB, failureCB);
        }.bind(this));
    },
    runRecovery: function runRecovery(docID, data, successCB, failureCB) {
        var params = {
            path: '/upload/drive/v2/files/' + docID + '/realtime',
            method: 'PUT',
            params: { uploadType: 'media', fileId: docID },
            headers: { 'Content-Type': 'text/plain;' },
            body: data
        };

        var request = gapi.client.request(params);

        var currentRecoverVersion = gdata.model.getRoot().get('recoverVersion') || 0;

        // Notify remote clients a recovery is happening.
        gdata.model.getRoot().set('recoverVersion', currentRecoverVersion + 1);

        gdata.clearItemEventListeners();

        request.then(function (success) {
            log('Recovery Success: ', success);

            g.settings.set(Settings.localRecoverVersion, currentRecoverVersion + 1);
            gdata.model.getRoot().set('recoverVersion', currentRecoverVersion + 1);

            d.addDefaultSkipSetting(Settings.gdocId, 'Default');
            d.addDefaultSkipSetting(Settings.gdocTitle, 'Default');

            gdata.close( /*forceClose*/true);

            successCB();
        }, function (failure) {
            log('Recovery Failure: ', failure);

            failureCB();
        });
    },
    getCurrentRevision: function getCurrentRevision() {
        var revision;

        try {
            revision = gdata.doc.getModel().serverVersion;
        } catch (err) {
            g.reportError(err);
        }

        return revision;
    },
    getCurrentDocID: function getCurrentDocID() {
        var docID;

        try {
            docID = g.settings.get(Settings.gdocId);
        } catch (err) {
            g.reportError(err);
        }

        return docID;
    },
    getRawRevisionData: function getRawRevisionData(docID, rev, _cb) {
        var gapiToken = AccountManager.getDefaultAccessToken();

        if (gapiToken) {
            var url = 'https://www.googleapis.com/drive/v2/files/' + (docID || this.getCurrentDocID()) + '/realtime';

            if (rev) {
                url += '?revision=' + rev;
            }

            g.XHR({
                type: 'GET',
                url: url,
                headers: { 'Authorization': 'Bearer ' + gapiToken },
                cb: function cb(xhr) {
                    if (xhr.status === 200) {
                        try {
                            var revData = JSON.parse(xhr.responseText);

                            _cb(revData);
                        } catch (err) {
                            g.reportError(err);

                            _cb();
                        }
                    } else {
                        _cb();
                    }
                }
            });
        } else {
            log('Invalid OAuth token');

            _cb();
        }

        // TODO: At some point in the future, google may fix this API in the gapi client.
        //
        // gapi.client.drive.realtime.get({
        //     fileId: docID || this.getCurrentDocID(),
        //     revision: rev
        // }).execute(function (obj)
        // {
        //     if (!obj || obj.error) { log('Error getting data from Drive: ', obj.error); return cb(); }

        //     if (cb)
        //     {
        //         cb(obj.result);
        //     }
        //     else
        //     {
        //         console.log('Missing CB function, printing to console.');

        //         log(obj.result);
        //     }
        // });
    },
    getDefaultRawRevisionData: function getDefaultRawRevisionData(rev, cb) {
        return this.getRawRevisionData(this.getCurrentDocID(), rev, cb);
    },
    transformRawRevisionData: function transformRawRevisionData(obj, perItemCB) {
        var data = ref(obj.data);

        if (data) {
            var traverse = function traverse(node, level) {
                if (level >= 0) {
                    perItemCB(node, level);
                }

                if (node.i) {
                    var _loopIt = 0;

                    var children = ref(node.i);

                    for (var i = 0; i < children.length; ++i) {
                        if (_loopIt++ > 5000 && __infLoop && __infLoop(_loopIt)) throw new RangeError();

                        var child = ref(children[i]);

                        if (child) {
                            traverse(child, level + 1);
                        }
                    }
                }

                if (node.n) {
                    var note = ref(node.n);

                    if (note) {
                        traverse(note, level + 1);
                    }
                }
            };

            var root = ref(data.root);

            traverse(root, -1);
        } else {
            g.reportError('Invalid Revision Data: ', obj);
        }
    },
    _makeConsumableOutput: function _makeConsumableOutput(docID, rev, perItemCB, doneCB) {
        this.getRawRevisionData(docID, rev, function (rawData) {
            try{g.PAssert(2421,rawData && rawData.data, 'Missing expected recovery data');}catch(PERR){g.reportError(PERR);}

            if (rawData && rawData.data) {
                try {
                    this.transformRawRevisionData(rawData, perItemCB);
                } catch (err) {
                    g.reportError(err);

                    rawData = undefined;
                }
            }

            if (doneCB) {
                doneCB(rawData);
            }
        }.bind(this));
    },
    _padText: function _padText(text, padding, padChar) {
        var _loopIt2 = 0;

        var outText = '';

        padChar = padChar || ' ';

        for (var i = 0; i < padding; ++i) {
            if (_loopIt2++ > 5000 && __infLoop && __infLoop(_loopIt2)) throw new RangeError();
            outText += padChar;
        }

        outText += text;

        return outText;
    },
    printRevisionData: function printRevisionData(docID, rev) {
        this._makeConsumableOutput(docID, rev, function (node, level) {
            console.log(this._padText(val(node.t), level));
        }.bind(this));
    },
    printDefaultRevisionData: function printDefaultRevisionData(rev) {
        return this.printRevisionData(this.getCurrentDocID(), rev);
    },
    printRevisionOffset: function printRevisionOffset(offset) {
        var rev = this.getCurrentRevision() - offset;

        if (rev < 0) {
            return log('Maximum offset is: ' + this.getCurrentRevision());
        }

        return this.printDefaultRevisionData(rev);
    },
    getRevisionData: function getRevisionData(docID, rev, format, cb) {
        var fullText = '';
        var currentLevel = -1;

        this._makeConsumableOutput(docID, rev, function (node, level) {
            if (!format || format === 'text') {
                fullText += this._padText(val(node.t), level, '\t') + '\r\n';
            } else if (format === 'html') {
                if (level > currentLevel) {
                    fullText += '<ul>';
                } else if (level < currentLevel) {
                    var _loopIt3 = 0;

                    for (var i = level; i < currentLevel; i++) {
                        if (_loopIt3++ > 5000 && __infLoop && __infLoop(_loopIt3)) throw new RangeError();

                        fullText += '</ul>';
                    }
                }

                fullText += '<li>' + val(node.t) + '</li>';

                currentLevel = level;
            }
        }.bind(this), function (rawData) {
            cb(fullText, rawData);
        });
    },
    saveRevisionData: function saveRevisionData(docID, rev, format) {
        var fullText = '';

        this._makeConsumableOutput(docID, rev, function (node, level) {
            fullText += this._padText(val(node.t), level, '\t') + '\r\n';
        }.bind(this), function () {
            var blob = new Blob([fullText], { type: 'text/plain;charset=utf-8' });

            saveAs(blob, 'DataRecovery_' + rev + '.txt');
        });
    },
    saveDefaultRevisionData: function saveDefaultRevisionData(rev, format) {
        return this.saveRevisionData(this.getCurrentDocID(), rev, format);
    },
    saveRevisionOffset: function saveRevisionOffset(offset, format) {
        var rev = this.getCurrentRevision() - offset;

        if (rev < 0) {
            return log('Maximum offset is: ' + this.getCurrentRevision());
        }

        return this.saveDefaultRevisionData(rev, format);
    },
    getHelp: function getHelp() {
        console.log('To recover your data input __MoodoRecovery.saveRevisionOffset(<revision offset>)');
        console.log('The maximum <revision offset> for this document is ' + this.getCurrentRevision());
        console.log('If you just lost data, an offset of 10 is a good place to start');
        console.log('You may need to try larger offsets if the returned file does not contain the data you need');
        console.log('This is a temporary tool to help recover data, please report any issues or data loss to support@moo.do');

        window.__SendInfo('Recover Help Used!');

        return '';
    },
    dumpRawIndexedDB: function dumpRawIndexedDB() {
        d.dbi.settings.get('root').done(function (rootResult) {
            var rootID = rootResult.root;

            d.dbi.items.query().all().execute().done(function (results) {
                var root;

                results.forEach(function (item) {
                    if (item.id === rootID) {
                        root = item;
                    }

                    if (!d.getItem(item.id)) {
                        d.setItem(item.id, g.translateObjOut(item));
                    }
                });

                var out = d.toJSON(root, false, false, true);

                saveAs(new Blob([out], { type: 'text/plain;charset=utf-8' }), 'MoodoRecovery.json');
            });
        });
    }
};

var rec = new Recovery();

window.__MoodoRecovery = {
    maxUIRevisions: 2000,
    raw: rec,
    help: rec.getHelp.bind(rec),
    getCurrentRevision: rec.getCurrentRevision.bind(rec),
    getCurrentDocID: rec.getCurrentDocID.bind(rec),
    getRawData: rec.getDefaultRawRevisionData.bind(rec),
    printRevision: rec.printDefaultRevisionData.bind(rec),
    saveRevision: rec.saveDefaultRevisionData.bind(rec),
    printRevisionOffset: rec.printRevisionOffset.bind(rec),
    saveRevisionOffset: rec.saveRevisionOffset.bind(rec),
    dumpRawIndexedDB: rec.dumpRawIndexedDB.bind(rec),
    _getRawRevisionData: rec.getRawRevisionData.bind(rec)
};

export default rec;